import React, { useEffect, useState, useRef, forwardRef } from 'react';

const AutoScrollingDiv = ({text}) => {
    
    const divRef = useRef(null);
    const textReference = useRef('');
    const scrollDelayTimer = useRef(0);
    
    useEffect(() => {
        const interval = setInterval(() => {
            if (divRef.current) {
                scrollDelayTimer.current = scrollDelayTimer.current + 4;
                if(scrollDelayTimer.current > 15){
                    divRef.current.scrollTop += 1; 
                }
            }
        }, 200);

        return () => {
            clearInterval(interval);
        }
    }, []);
    
    useEffect(() => {
        if (textReference.current !== text) {
            textReference.current = text;
            divRef.current.scrollTop = 0;
            scrollDelayTimer.current = 0;
        }
    }, [text]);
 
    return (
        <div ref={divRef} className="game-npc-text" style={{top: '0px', left: '0px', width: '100%', height: '100%'}}>{text}</div>
    );
    
}

export default AutoScrollingDiv;