import React, { useState, useRef, forwardRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import { apiUrl } from '../../config.js'
import axios from 'axios';

const SearchBar = forwardRef(({ setSearching, openStream}, ref) => {
    
    const { setSubMenuActiveStatus } = useGlobalState();

    const [searchQuery, setSearchQuery] = useState('');
    const [loading, setLoading] = useState(true);
    
    const [loadingText, setLoadingText] = useState('');
    
    const playlist = useRef(null);
    
    const loadingTime = useRef(false);
    
    const loadingActive = useRef(false);
    
    const searchQueryReference = useRef('');
    
    const handleSearch = async (enquiry) => {
        
        if(enquiry === ''){
            enquiry = 'null and void';
        }
        axios.get(`${apiUrl}/api/search/enquiry/${enquiry}`)
            .then(async (response) => {
                console.log(enquiry);
                console.log(response.data);
                playlist.current = response.data.map((item) => ({
                    videoId: item.videoId,
                    title: item.title,
                    description: item.description,
                    thumbnail: item.thumbnail,
                    genre: item.genre,
                    director: item.director,
                    writer: item.writer,
                    cast: item.cast,
                    release: item.release,
                    rating: item.rating,
                    imdb: item.imdb,
                }));
                if(playlist.current.length === 0){
                    setLoadingText('No Results Found');
                }
                if(playlist.current.length > 0){
                    setLoadingText('');
                }

                loadingActive.current = false;
                setLoading(false);
            })
            .catch((error) => console.error('Error fetching search query:', error));
    };
    
    const updateSearch = (searchValue) => {
        searchQueryReference.current = searchValue;
        setSearchQuery(searchValue);
        loadingTime.current = Date.now();
        if(loadingActive.current === false){
            loadingActive.current = true;
            timeDelaySearch(searchValue);
        }
    }
    
    const timeDelaySearch = () => {
        if(Date.now() - loadingTime.current > 1000){
            handleSearch(searchQueryReference.current);
            setLoadingText('Loading');
            setLoading(true);
        }
        else{
            setTimeout(() => {
                timeDelaySearch();
            }, 1000);
        }
    }

    return (
        <div className="video-player-search-bar">
            <input
                className="video-player-search-bar-input"
                type="text"
                value={searchQuery}
                onChange={(e) => updateSearch(e.target.value)}
                placeholder="Enter Search Text" 
            />
            <div className="video-player-search-bar-display">
                {loading ? (
                        <p className="video-player-search-bar-text">{loadingText}</p>
                    ) : (
                        <>
                            {playlist.current.map((video, index) => (
                                <div key={index}
                                    className="video-player-search-bar-item"
                                    onClick={() => openStream('0', video.videoId, true)}
                                >
                                    <img className="video-player-search-bar-item-image" src={video.thumbnail.medium.url} alt={video.title} />
                                </div>
                            ))}
                            <p className="video-player-search-bar-text">{loadingText}</p>
                        </>
                    )
                }
                
            </div>
            <div className="video-player-search-bar-close-button"
                onClick={() => {setSearching(false); setSubMenuActiveStatus(false);}}
            >
                <div className="video-player-search-bar-close-button-image"></div>
            </div>
        </div>
    );
});

export default SearchBar;