import React from 'react';

let slots = [];
let verticalSliders = [];
let horizontalSliders = [];
let categoryDisplays = [];
let displayScreen = {};
let informationScreen = {};

const lerpSpeedDuration = 200;

const sliderTopPosition = 0;
const sliderBottomPosition = 90;

const sliderLeftPosition = -1;
const sliderRightPosition = 91;

const slidersColumnRespace = .21;

const categoryDisplayColumnRespace = .57;

const columnStepSpace = 25;
const columnStepRespace = .56;

const rowStepSpace = 18;
const rowStepRespace = .71;

const columnSlotCount = 5;
const rowSlotCount = 7;

let slotX = 0;
let slotY = 0;
const slotRotationX = 0;
const slotRotationY = 0;
const slotRotationZ = 0;
const slotSkewX = 0;
const slotSkewY = 0;
const slotWidth = 17;
const slotHeight = 23;
let slotOpacity = 1;
const slotPerspective = 0;

const categoryDisplayPositionX = 46;

let titleSize = {};

const initializeSlots = () => {
    for(let column = 0; column < columnSlotCount; column++){
        slots.push([]);
        for(let row = 0; row < rowSlotCount; row++){
            slotX = 0;
            if(row === 0){
                slotX = -100;
                //slotX = -rowStepSpace * 2;
            }
            if(row > 0 && row < rowSlotCount - 1){
                slotX = (row * rowStepSpace) - (rowStepSpace * rowStepRespace);
            }
            if(row === rowSlotCount - 1){
                slotX = 200;
                //slotX = row * rowStepSpace + rowStepSpace;
            }

            slotY = 0;
            if(column === 0){
                slotY = -150;
                //slotY = -columnStepSpace * 2;
            }
            if(column > 0 && column < columnSlotCount - 1){
                slotY = (column * columnStepSpace) - (columnStepSpace * columnStepRespace);
            }
            if(column === columnSlotCount - 1){
                slotY = 250;
                //slotY = 100 + (columnStepSpace * 2);
            }
            
            slotOpacity = 1;
            if(column === 0 || column === columnSlotCount - 1 || row === 0 || row === rowSlotCount - 1){
                slotOpacity = 0;
            }

            slots[column].push({x: slotX, y: slotY, rx: slotRotationX, ry: slotRotationY, rz: slotRotationZ, sx: slotSkewX, sy: slotSkewY, w: slotWidth, h: slotHeight, o: slotOpacity, p: slotPerspective});
        }
    }
}

initializeSlots();

const initializeVerticalSliders = () => {

    verticalSliders.push({descend: {position: {x:-100, y: sliderBottomPosition}, rotation: {x:   0, y:   0, z: 180}}, ascend: {position: {x:-100, y: sliderTopPosition}, rotation: {x:   0, y:   0, z:   0}}});
    verticalSliders.push({descend: {position: {x:  45, y: sliderBottomPosition}, rotation: {x:   0, y:   0, z: 180}}, ascend: {position: {x:  45, y: sliderTopPosition}, rotation: {x:   0, y:   0, z:   0}}});
    verticalSliders.push({descend: {position: {x: 200, y: sliderBottomPosition}, rotation: {x:   0, y:   0, z: 180}}, ascend: {position: {x: 200, y: sliderTopPosition}, rotation: {x:   0, y:   0, z:   0}}});
}

initializeVerticalSliders();
    
const initializeHorizontalSliders = () => {
    for(let column = 0; column < columnSlotCount; column++){
        let columnStep = column * columnStepSpace;

        if(column === 0){
            columnStep = -150
            //columnStep = -columnStepSpace * 2;
        }
        if(column > 0 && column < columnSlotCount - 1){
            columnStep = (column * columnStepSpace) - (columnStepSpace * slidersColumnRespace);
        }
        if(column === columnSlotCount - 1){
            columnStep = 250;
            //columnStep = 100 + (columnStepSpace * 2);
        }


        horizontalSliders.push({descend: {position: {x: sliderLeftPosition, y: columnStep}, rotation: {x:   0, y:   0, z: 270}}, ascend: {position: {x: sliderRightPosition, y: columnStep}, rotation: {x:  0, y:   0, z:  90}}});

    }
}

initializeHorizontalSliders();

const initializeCategoryDisplays = () => {
    for(let column = 0; column < columnSlotCount; column++){
        let columnStep = column * columnStepSpace;

        if(column === 0){
            columnStep = -150;
            //columnStep = -columnStepSpace * 2;
        }
        if(column > 0 && column < columnSlotCount - 1){
            columnStep = (column * columnStepSpace) - (columnStepSpace * categoryDisplayColumnRespace) - 5;
        }
        if(column === columnSlotCount - 1){
            columnStep = 250;
            //columnStep = 100 + (columnStepSpace * 2);
        }


        categoryDisplays.push({
            standalone: {position: {x: categoryDisplayPositionX, y: columnStep}, rotation: {x:   0, y:   0, z:   0}, width: 8, height: 8, fontSize:1.62},
            mobile:     {position: {x: categoryDisplayPositionX, y: columnStep}, rotation: {x:   0, y:   0, z:   0}, width: 8, height: 8, fontSize:  .9},
        });
    }
}

initializeCategoryDisplays();

const initializeDisplayScreen = () => {
    displayScreen = {
        standalone: {x: -125, y: -10, rx: 0, ry: 0, rz: 0, w: 350, h: 100},
        mobile:     {x: -125, y: -10, rx: 0, ry: 0, rz: 0, w: 350, h: 100}
    };
}

initializeDisplayScreen();

const initializeInformationScreen = () => {
    informationScreen = {
        standalone: {x: -5, y: -7, rx: 0, ry: 0, rz: 0, w: 110, h: 110},
        mobile:     {x: -5, y: -7, rx: 0, ry: 0, rz: 0, w: 110, h: 110}
    };
}

initializeInformationScreen();

const initializeTitleSize = () => {
    titleSize = {
        standalone: 54,
        mobile:     12
    };
}

initializeTitleSize();

const Linear = {
    slots: slots,
    columnStepSpace: columnStepSpace,
    rowStepSpace: rowStepSpace,
    columnSlotCount: columnSlotCount,
    rowSlotCount: rowSlotCount,
    verticalSliders: verticalSliders,
    horizontalSliders: horizontalSliders,
    displayScreen: displayScreen,
    informationScreen: informationScreen,
    lerpSpeedDuration: lerpSpeedDuration,
    categoryDisplays: categoryDisplays,
    titleSize: titleSize,
}

export { Linear };