export default () => {
        
    const base = self; // eslint-disable-line no-restricted-globals
    
    let debug = false;
    let startTime = 0;
    let endTime = 0;
    
    const topDistanceRatioX = 23;
    const topDistanceRatioY = 13;

    const characterLightOffsetX = 0;
    const characterLightOffsetY = 10;

    let lightAngle = 0;

    const maximumRenderDistance = 800;
    const maximumShadowRenderDistance = 800;

    let deviationX = 0;
    let deviationY = 0;

    let angle = 0;

    let flickerCount = 0;
    
    let windowData;
    let gameReferenceData;
    let playerPhysicsData;

    let lights = [];
    let objects = [];
    let avatars = [];
    
    let offscreenLayerLight;
    
    let offscreenLayerLightSection;
    
    let layerLight;
    
    let layerLightSection;
        
    let layerContainerSectionTop;
    let layerContainerSectionLeft;
    
    let queued = false;
    
    let lightToLightDistanceX;
    let lightToLightDistanceY;
    let layerLightBitmapData;
    let y;
    let x;
    let dist;
    let rads;
    let pointPos;
    let DEBUGTRIP;
    let curAngle;
    let dynLen;
    let addTo;
    let findDistRes;
    let lightAdjustedPositionX;
    let lightAdjustedPositionY;
    let objectLightDistanceX;
    let objectLightDistanceY;
    let mid;
    let end;

    base.onmessage = (event) => { 
        processMessage(event.data);
    }

    const processMessage = (eventData) => {
        if(eventData.type == "Initialize"){
            initialize(eventData);
        }
        if(eventData.type == "Update"){
            update(eventData);
        }
    }

    /* Movement - Collision - Animation */
    const initialize = (eventData) => {
        offscreenLayerLight = eventData.offscreenLayerLight;

        offscreenLayerLightSection = eventData.offscreenLayerLightSection;

        offscreenLayerLight.width = eventData.payload.gameReferenceData.gameContainerWidth;
        offscreenLayerLight.height = eventData.payload.gameReferenceData.gameContainerHeight;
  
        layerLight = offscreenLayerLight.getContext("2d");

        animate();
    }
    
    /* Movement - Collision - Animation */
    const update = (eventData) => {
        startTime = new Date().getTime();

        debug = eventData.payload.debug;
        windowData = eventData.payload.windowData;
        gameReferenceData = eventData.payload.gameReferenceData;
        playerPhysicsData = eventData.payload.playerPhysicsData;
        
        lights = eventData.payload.engineData.levelData.lights;
        objects = eventData.payload.playerPriorityData.objectPriorityList;
        avatars = eventData.payload.engineData.levelData.avatars;
        
        queued = true;
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Render Data Update Clock: " + endTime);
        }
    }
    
    /* Movement - Collision - Animation */
    const animate = () => {
        if(queued){
            queued = false;
            startTime = new Date().getTime();

            //layerLight.clearRect(0, 0, windowData.windowWidth, windowData.windowHeight);
            //offscreenLayerLight.width = offscreenLayerLight.width;
            //offscreenLayerLight.height = offscreenLayerLight.height;
            layerLight.fillStyle = 'rgba(0, 0, 0, 0)';
            layerLight.fillRect(0, 0, windowData.windowWidth, windowData.windowHeight);

            lightAngle+=0.01;
            for(var i = 0; i < lights.length; i++){
                layerLight.strokeStyle = lights[i].color;
                lights[i].angle+=46;
                /* Adjust character light && object */
                if(i == 0){
                    lights[i].position.x = playerPhysicsData.playerOffsetX + windowData.windowHalfWidth + characterLightOffsetX;
                    lights[i].position.y = playerPhysicsData.playerOffsetY + windowData.windowHalfHeight + characterLightOffsetY;
                    avatars[i].position.x = lerp(avatars[i].position.x, lights[i].position.x - 2, .5);
                    avatars[i].position.y = lerp(avatars[i].position.y, lights[i].position.y + 30, .5);
                }
                lightToLightDistanceX = lights[i].position.x - playerPhysicsData.playerOffsetX;
                lightToLightDistanceY = lights[i].position.y - playerPhysicsData.playerOffsetY;
                if(Math.abs(lightToLightDistanceX) < maximumRenderDistance && Math.abs(lightToLightDistanceY) < maximumRenderDistance){
                    drawLight(lights[i], i);  
                }
            }
            
            
            layerLightBitmapData = offscreenLayerLight.transferToImageBitmap();

            base.postMessage({type: "Game Render Data", layerLightBitmapData: layerLightBitmapData}); // eslint-disable-line no-restricted-globals

            if(debug){
                endTime = new Date().getTime() - startTime;
                console.log("Render Clock: " + endTime);
            }
            
        }
        base.currentRefId = base.requestAnimationFrame(animate);
    }
    
    function lerp(start, end, t){
        return start * (1 - t) + end * t;
    }

    /* Find Distance */
    function findDistance(light, object, angle, rLen, start, shortest, closestBlock){
        y = (object.position.y + object.height/2) - light.position.y;
        x = (object.position.x + object.width/2) - light.position.x;
        dist = Math.sqrt((y * y) + (x * x));

        if(light.radius >= dist)
        {
            rads = angle * (Math.PI / 180);
            pointPos = {
                x: light.position.x,
                y: light.position.y
            };

            pointPos.x += Math.cos(rads) * dist;
            pointPos.y += Math.sin(rads) * dist;

            if(pointPos.x > object.position.x && pointPos.x < object.position.x + object.width && pointPos.y > object.position.y && pointPos.y < object.position.y + object.height)
            {
                if(start || dist < shortest){
                    start = false;
                    shortest = dist;
                    rLen= dist;
                    closestBlock = object;
                }
                return {'start' : start, 'shortest' : shortest, 'rLen' : rLen, 'object' : closestBlock};
            }
        }
        return {'start' : start, 'shortest' : shortest, 'rLen' : rLen, 'object' : closestBlock};
    }

    /* Shine Light */
    function drawLight(light, index){
        var DEBUGTRIP = false;
        curAngle = light.angle - (light.angleSpread/2);
        dynLen = light.radius;
        addTo = 1/light.radius;

        for(curAngle; curAngle < light.angle + (light.angleSpread/2); curAngle += (addTo * (180/Math.PI))*2){
            dynLen = light.radius;

            findDistRes = {};

            findDistRes.start = true;
            findDistRes.shortest = 0;
            findDistRes.rLen = dynLen;
            findDistRes.object = {};

            lightAdjustedPositionX = light.position.x - playerPhysicsData.playerOffsetX;
            lightAdjustedPositionY = light.position.y - playerPhysicsData.playerOffsetY;
            
            if(light.type == "Point"){
                for(var i = 0; i < objects.length; i++)
                {
                    objectLightDistanceX = objects[i].position.x - light.position.x;
                    if(objectLightDistanceX < maximumRenderDistance && objectLightDistanceX > -maximumRenderDistance && objects[i].type != "Invisible"){
                        objectLightDistanceY = objects[i].position.y - light.position.y;
                        if(objectLightDistanceY < maximumRenderDistance && objectLightDistanceY > -maximumRenderDistance){
                            findDistRes = findDistance(light, objects[i], curAngle, findDistRes.rLen, findDistRes.start, findDistRes.shortest, findDistRes.object);
                        }
                    }
                }
                
                for(var i = 0; i < avatars.length; i++)
                {
                    if(i != 0){
                        objectLightDistanceX = avatars[i].position.x - light.position.x;
                        if(objectLightDistanceX < maximumRenderDistance && objectLightDistanceX > -maximumRenderDistance){
                            objectLightDistanceY = avatars[i].position.y - light.position.y;
                            if(objectLightDistanceY < maximumRenderDistance && objectLightDistanceY > -maximumRenderDistance){
                                findDistRes = findDistance(light, avatars[i], curAngle, findDistRes.rLen, findDistRes.start, findDistRes.shortest, findDistRes.object);
                            }
                        }
                    }
                }
            }
            if(light.type == "Static"){
                objectLightDistanceX = objects[0].position.x - light.position.x;
                if(objectLightDistanceX < maximumRenderDistance && objectLightDistanceX > -maximumRenderDistance && objects[0].type != "Invisible"){
                    objectLightDistanceY = objects[0].position.y - light.position.y;
                    if(objectLightDistanceY < maximumRenderDistance && objectLightDistanceY > -maximumRenderDistance){
                        findDistRes = findDistance(light, objects[0], curAngle, findDistRes.rLen, findDistRes.start, findDistRes.shortest, findDistRes.object);
                    }
                }
                for(var i = 0; i < avatars.length; i++)
                {
                    objectLightDistanceX = avatars[i].position.x - light.position.x;
                    if(objectLightDistanceX < maximumRenderDistance && objectLightDistanceX > -maximumRenderDistance){
                        objectLightDistanceY = avatars[i].position.y - light.position.y;
                        if(objectLightDistanceY < maximumRenderDistance && objectLightDistanceY > -maximumRenderDistance){
                            findDistRes = findDistance(light, avatars[i], curAngle, findDistRes.rLen, findDistRes.start, findDistRes.shortest, findDistRes.object);
                        }
                    }
                }
            }

            /* Check if character light */
            if(index == 0){
                if(findDistRes.rLen < maximumShadowRenderDistance){
                    rads = curAngle * (Math.PI / 180);
                    mid = {
                        x: lightAdjustedPositionX,
                        y: lightAdjustedPositionY
                    };
                    end = {
                        x: lightAdjustedPositionX,
                        y: lightAdjustedPositionY
                    };

                    findDistRes.object.visible = true;
                    mid.x += Math.cos(rads) * findDistRes.rLen;
                    mid.y += Math.sin(rads) * findDistRes.rLen;
                    end.x += Math.cos(rads) * light.radius;
                    end.y += Math.sin(rads) * light.radius;

                    layerLight.beginPath();
                    layerLight.moveTo(mid.x, mid.y);
                    layerLight.lineTo(end.x, end.y);
                    layerLight.closePath();

                    layerLight.stroke();
                }
                
                /*
                
                var rads = curAngle * (Math.PI / 180),
                    end = {
                        x: lightAdjustedPositionX,
                        y: lightAdjustedPositionY
                    };

                findDistRes.object.visible = true;
                end.x += Math.cos(rads) * findDistRes.rLen;
                end.y += Math.sin(rads) * findDistRes.rLen;

                layerLight.beginPath();
                layerLight.moveTo(lightAdjustedPositionX, lightAdjustedPositionY);
                layerLight.lineTo(end.x, end.y);
                layerLight.closePath();
                
                layerLight.stroke();
                
                */
                
            }
            /* Check if not character light */
            if(index != 0){
                rads = curAngle * (Math.PI / 180);
                end = [{
                    x: lightAdjustedPositionX,
                    y: lightAdjustedPositionY
                }];

                findDistRes.object.visible = true;
                end.x += Math.cos(rads) * findDistRes.rLen;
                end.y += Math.sin(rads) * findDistRes.rLen;

                layerLight.beginPath();
                layerLight.moveTo(lightAdjustedPositionX, lightAdjustedPositionY);
                layerLight.lineTo(end.x, end.y);
                layerLight.closePath();
                layerLight.stroke();
            }
        }
    }
}