import React, { useState, useLayoutEffect, useRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import { useNavigate } from 'react-router-dom';


const Loadingscreen = () => {
    
    const { mobileActiveStatus, engineActiveStatus, videoPlayerActiveStatus, chatRoomActiveStatus } = useGlobalState();
    
    const [isLoading, setIsLoading] = useState(true);
    
    const initialized = useRef(false);
    
    const loadingScreenReference = useRef(null);
    
    const fadeDuration = useRef(3);
    
    const navigate = useNavigate();

    useLayoutEffect(() => {
        // AI ENGINE REMOVE NEXT ENABLE FOLLOWING
        if(videoPlayerActiveStatus >= 0 && initialized.current === false){
        //if((engineActiveStatus >= 0 || videoPlayerActiveStatus >= 0 || chatRoomActiveStatus >= 0) && initialized.current === false){
            initialized.current = true;
            console.log("Initialized")
            fadeOutLoadingScreen();
        }
    }, [engineActiveStatus, videoPlayerActiveStatus, chatRoomActiveStatus]);

    
    const fadeOutLoadingScreen = () => {
        navigate('/');
        console.log("Fading Out");
        const element = loadingScreenReference.current;
        element.style.opacity = "0";
        element.style.transition = `opacity ${fadeDuration.current}s`;

        setTimeout(() => {
            console.log("Loaded");
            setIsLoading(false);
        }, fadeDuration.current * 1000);
  };

    return (
        <div className="loading-screen">
            {isLoading && (
                <div ref={(ref) => (loadingScreenReference.current = ref)} className="loading-screen-container">
                    <div className="loading-screen-container-image">
                        <div className="loading-screen-icon-container"
                            style={{
                                top: mobileActiveStatus ? '32vh' : '26vh'
                            }}
                        >
                            <div className="loading-screen-icon-image" style={{animation: 'rotateZRight 4s linear infinite'}}>
                                <div className="loading-screen-icon-sub-image" style={{animation: 'rotateXRight 4s linear infinite'}}>
                                    <div className="loading-screen-icon-sub-image" style={{animation: 'rotateXRight 4s linear infinite'}}>
                                        <div className="loading-screen-icon-sub-image" style={{animation: 'rotateYRight 4s linear infinite'}}>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>                    
                </div>
            )}
        </div>
    );
};

export default Loadingscreen;