import React, { useState, useEffect, Component } from 'react';
import Speech from '../Speech/index.js';
import GameManager from '../GameManager';
import { Lights } from '../Lights';
import { Objects } from '../Objects';
import { Avatars } from '../Avatars';
import { NonPlayerCharacters } from '../NonPlayerCharacters';
import '../../App.css';

const EngineMain = () => {

    const props = {
        references: {
            engineManagerReference: React.useRef(null),
            gameContainerReference: React.useRef(null),
            gameOffscreenLayerLightReference: React.useRef(null),
            gameOffscreenLayerObjectReference: React.useRef(null),
            gameOffscreenLayerLightSectionReference: React.useRef(null),
            gameOffscreenLayerObjectSectionReference: React.useRef(null),
            gameLayerLightReference: React.useRef(null),
            gameLayerObjectReference: React.useRef(null), 
            gameLevelContainerReference: React.useRef(null),
            gameBorderWallReference: React.useRef(null),
            gameSubTerrainReference: React.useRef(null),
            gameLandscapeReference: React.useRef(null),
            gameLandscapeShadowmapReference: React.useRef(null),
            gameTerrestialTerrainReference: React.useRef(null),
            gameLinkContainerReference: React.useRef(null),
            gamePlayerContainerReference: React.useRef(null),
            gamePlayerAvatarReference: React.useRef(null),
            gamePlayerFresnelReference: React.useRef(null),
            gamePlayerShadowmapReference: React.useRef(null),
            gameNPCLevelContainerReference: React.useRef(null),
            gameNPCContainerReference: React.useRef(null),
            gameNPCAvatarReference: React.useRef(null),
            gameNPCMouthReference: React.useRef(null),
            gameNPCFresnelReference: React.useRef(null),
            gamePlayerBloomInnerReference: React.useRef(null),
            gamePlayerBloomMiddleReference: React.useRef(null),
            gamePlayerBloomOutterReference: React.useRef(null),
            gameAerialTerrainReference: React.useRef(null),

            gameObjectManagerReference: React.useRef(null),
            
            voiceReference: React.useRef(""),
            
            images: {
                cageTopImageReference: React.useRef(null),
                cageBottomImageReference: React.useRef(null),
                cageBottomDimImageReference: React.useRef(null),
                portalTopImageReference: React.useRef(null),
                portalBottomImageReference: React.useRef(null),
                portalBottomDimImageReference: React.useRef(null),
                altarTopImageReference: React.useRef(null),
                altarBottomImageReference: React.useRef(null),
                altarBottomDimImageReference: React.useRef(null),
                wallTopImageReference: React.useRef(null),
                wallBottomImageReference: React.useRef(null),
                wallBottomDimImageReference: React.useRef(null),
                pillarTopImageReference: React.useRef(null),
                pillarBottomImageReference: React.useRef(null),
                pillarBottomDimImageReference: React.useRef(null),
            }
        },
        
        engineData: React.useRef({
            debug: false,
            initialAerialOffset: 1536,
            mapWidth: 6144,
            mapHeight: 6144,
            mapWidthQuadrantCount: 3,
            mapHeightQuadrantCount: 3,
            playerPhysicsData: {
                playerMovementSpeed: 20,
                playerOffsetX: 2700,
                playerOffsetY: 2700,
            },
            levelData: {
                lights: Lights,
                objects: Objects,
                avatars: Avatars,
            },
            nonPlayerCharacterData: {
                nonPlayerCharacters: NonPlayerCharacters,
            }
        }),
    };
    
    return (
        <div className="engine-frame">
            <div className="engine-components">
                <Speech props={props} />
                <GameManager props={props} />
            </div>
            <div className="engine-content">

            </div>
        </div>
    );    
}
          
                        
export default EngineMain;   