import React, { useState, useEffect, useRef, forwardRef } from 'react';
import ReactDOM from 'react-dom';
import { useGlobalState  } from '../../globalcontext';
import VideoPlayerSlider from '../VideoPlayerSlider';
import Recommendations from '../Recommendations';

const VideoIFramePlayer = forwardRef(({ videoId, playTime, getPlayTime, getDuration, toggleFullScreen, onPlayChange, frameAnchorReference, frameSizeReference, frameResolutionReference, frameBaseReference, frameWrapperReference, frameContainerReference, iframeBaseReference, videoPlayerOverlayReference, toggleVideoPlay, videoPlayImageReference, videoPauseImageReference, openStream, userId}, ref) => {
        
    const { mobileActiveStatus } = useGlobalState();

    const playerRef = useRef(null);

    const iframeAnchorReference = useRef(null);
    const iframeSizeReference = useRef(null);
    const iframeResolutionReference = useRef(null);
    const iframeWrapperReference = useRef(null);
    const iframeContainerReference = useRef(null);
    const iframeControllerReference = useRef(null);
    const iframeControllerActivatorReference = useRef(null);
    
    const currentVideoId = useRef('');
    
    const [recommending, setRecommending] = useState(false);
    
    const intervalReference = useRef(null);
    const currentTime = useRef(null);
    const duration = useRef(null);
    const progress = useRef(null);
    
    const timers = useRef([
        { name: 'Frame Timer', duration: 36, value: 0 },
        { name: 'Control Timer', duration: 71, value: 0 },
    ]);
    
    useEffect(() => {
        const script = document.createElement('script');
        script.src = 'https://www.youtube.com/iframe_api';
        document.body.appendChild(script);
        window.onYouTubeIframeAPIReady = () => {
            playerRef.current = new window.YT.Player('player', {
                height: '100%',
                width: '100%',
                videoId: videoId,
                playerVars: {
                    controls: 0,
                    showinfo: 0,
                    rel: 0,
                    modestbranding: 1,
                    enablejsapi: 1,
                    host: window.location.host,
                    origin: window.location.origin
                },
                events: {
                    'onStateChange': onPlayerStateChange
                }
            });
            ref.current = playerRef.current;
            console.log("Made New");
        };                                        

        return () => {
            document.body.removeChild(script);
        };
    }, [ref]);
    
    useEffect(() => {
        
        const interval = setInterval(() => {
            timers.current.forEach(timer => {
                timer.value += 10;
                if(timer.value >= timer.duration){
                    timer.value = 0;
                    
                    processTimer(timer.name);
                
                }
            });
        }, 10);

        document.addEventListener('mousemove', activateController);

        return () => {
            document.removeEventListener('mousemove', activateController);
        };
        
    }, [ ]);
    
    const processTimer = (timer) => {
        
        if(timer === "Frame Timer"){
            adjustFrames();
        };
        
        if(timer === "Control Timer"){
            adjustControls();
        };
    }
    
    const controlStartTime = useRef(null);
    const controlCurrentTime = useRef(null);
    const controlDuration = useRef(3500);
    const initiatedController = useRef(null);
    
    const adjustControls = () => {
        if(controlStartTime.current === null){
            controlStartTime.current = new Date().getTime();
        }
        controlCurrentTime.current = new Date().getTime();
        if(initiatedController.current === true && controlCurrentTime.current - controlStartTime.current >= controlDuration.current){
            
            initiatedController.current = false;
            iframeControllerReference.current.style.opacity = 0;
            iframeControllerActivatorReference.current.style.zIndex = 8204;
            iframeControllerActivatorReference.current.style.pointerEvents = 'auto';
        }
    }
    
    const activateController = () => {
        iframeControllerReference.current.style.opacity = 1;
        iframeControllerActivatorReference.current.style.zIndex = 800;
        iframeControllerActivatorReference.current.style.pointerEvents = 'none';
        controlStartTime.current = null;
        initiatedController.current = true;
    }
    
    const adjustFrames = () => {
        if(iframeAnchorReference.current && frameAnchorReference.current){

            iframeAnchorReference.current.style.top = frameAnchorReference.current.style.top;
            iframeAnchorReference.current.style.left = frameAnchorReference.current.style.left;
            iframeAnchorReference.current.style.width = frameAnchorReference.current.style.width;
            iframeAnchorReference.current.style.height = frameAnchorReference.current.style.height;

            iframeSizeReference.current.style.top = frameSizeReference.current.style.top;
            iframeSizeReference.current.style.left = frameSizeReference.current.style.left;
            iframeSizeReference.current.style.width = frameSizeReference.current.style.width;
            iframeSizeReference.current.style.height = frameSizeReference.current.style.height;

            iframeResolutionReference.current.style.top = frameResolutionReference.current.style.top;
            iframeResolutionReference.current.style.left = frameResolutionReference.current.style.left;
            iframeResolutionReference.current.style.width = frameResolutionReference.current.style.width;
            iframeResolutionReference.current.style.height = frameResolutionReference.current.style.height;

            iframeBaseReference.current.style.top = frameBaseReference.current.style.top;
            iframeBaseReference.current.style.left = frameBaseReference.current.style.left;
            iframeBaseReference.current.style.width = frameBaseReference.current.style.width;
            iframeBaseReference.current.style.height = frameBaseReference.current.style.height;

            iframeWrapperReference.current.style.top = frameWrapperReference.current.style.top;
            iframeWrapperReference.current.style.left = frameWrapperReference.current.style.left;
            iframeWrapperReference.current.style.width = frameWrapperReference.current.style.width;
            iframeWrapperReference.current.style.height = frameWrapperReference.current.style.height;

            iframeContainerReference.current.style.top = frameContainerReference.current.style.top;
            iframeContainerReference.current.style.left = frameContainerReference.current.style.left;
            iframeContainerReference.current.style.width = frameContainerReference.current.style.width;
            iframeContainerReference.current.style.height = frameContainerReference.current.style.height;

        }
    }
    
    const onPlayerStateChange = (event) => {
        if (event.data === window.YT.PlayerState.PLAYING) {
            onPlayChange(true);
            intervalReference.current = setInterval(() => {
                currentTime.current = playerRef.current.getCurrentTime();
                duration.current = playerRef.current.getDuration();
                progress.current = (currentTime.current / duration.current) * 100;
                if (progress.current >= 99.53) {
                    setRecommending(true);
                }
                if (progress.current < 99.53) {
                    setRecommending(false);
                }
            }, 1000);
        } else if(event.data === window.YT.PlayerState.PAUSED){
            onPlayChange(false);
            clearInterval(intervalReference.current);
        }
    };
    
    return (
        <div className="video-player-frame-iframe-container-buffer">
            <div className="video-player-frame-iframe-container-anchor"
                ref={iframeAnchorReference}
            >
                <div className="video-player-frame-iframe-container-size"
                    ref={iframeSizeReference}
                >
                    <div className="video-player-frame-iframe-container-resolution"
                        ref={iframeResolutionReference}
                    >
                        <div className="video-player-frame-iframe-container-base"
                            ref={iframeBaseReference}
                        >
                            <div className="video-player-frame-iframe-container-wrapper"
                                ref={iframeWrapperReference}
                            >
                                <div className="video-player-frame-iframe-container"
                                    ref={iframeContainerReference}
                                >
                                    <div id="player" />
                                </div>
                            </div>
                            <div className="video-player-frame-overlay" ref={videoPlayerOverlayReference}>
                                <div className="video-player-frame-overlay-top">
                                    <div className="video-player-frame-overlay-top-container">
                                        <div className="video-player-frame-overlay-top-image"
                                            style={{
                                                top: mobileActiveStatus ? '0%' : '-25%'
                                            }}
                                        >
                                        </div>
                                    </div>
                                </div>
                                <div className="video-player-frame-overlay-bottom">
                                    <div className="video-player-frame-overlay-bottom-container">
                                        <div className="video-player-frame-overlay-bottom-image"
                                            style={{
                                                top: mobileActiveStatus ? '0%' : '25%'
                                            }}
                                        >
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="video-player-frame-controller"
                                ref={iframeControllerReference}
                            >
                                <div className="video-player-frame-controls">
                                    <VideoPlayerSlider alterPlayTime={playTime} requestPlayTime={getPlayTime} requestDuration={getDuration} ref={ref} />
                                    <div className="video-player-frame-controls-play-button" onClick={() => toggleVideoPlay()}>
                                        <div className="video-player-frame-controls-play-image"
                                            ref={videoPlayImageReference}
                                        >
                                        </div>
                                        <div className="video-player-frame-controls-pause-image"
                                            ref={videoPauseImageReference}
                                        >
                                        </div>
                                    </div>
                                    <div className="video-player-frame-logo-container">
                                        <div className="video-player-frame-logo-image"></div>
                                    </div>
                                    <div className="video-player-frame-screen-button" onClick={() => toggleFullScreen()}>
                                        <div className="video-player-frame-screen-button-image"></div>
                                    </div>
                                </div>
                                <div className="video-player-frame-control-activator"
                                    ref={iframeControllerActivatorReference}
                                    onClick={() => activateController()}
                                >
                                </div>
                            </div>
                            {recommending && (
                                <Recommendations openStream={openStream} setRecommending={setRecommending} userId={userId}/>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
});

export default VideoIFramePlayer;