import React, { useState, useEffect, useRef, Component } from 'react';
import '../../App.css';
import { useGlobalState  } from '../../globalcontext';

const Close = () => {
    const { engineActiveStatus, setEngineActiveStatus } = useGlobalState();
    
    const { videoPlayerActiveStatus, setVideoPlayerActiveStatus } = useGlobalState();

    const { chatRoomActiveStatus, setChatRoomActiveStatus } = useGlobalState();

    const { subMenuActiveStatus } = useGlobalState();

    const [closeOptional, setCloseOptional] = useState(false);
    
    const activeComponent = useRef('');
    
    useEffect(() => {
        if((videoPlayerActiveStatus === 3 || engineActiveStatus === 3 || chatRoomActiveStatus === 3) && subMenuActiveStatus === false){
            setCloseOptional(true);
            if(videoPlayerActiveStatus === 3){
                activeComponent.current = 'Video Player';
            }
            if(engineActiveStatus === 3){
                activeComponent.current = 'Engine';
            }
            if(chatRoomActiveStatus === 3){
                activeComponent.current = 'Chat';
            }
        }
        if((videoPlayerActiveStatus !== 3 && engineActiveStatus !== 3 && chatRoomActiveStatus !== 3) || subMenuActiveStatus === true){
            setCloseOptional(false);
        }
        
    }, [engineActiveStatus, videoPlayerActiveStatus, chatRoomActiveStatus, subMenuActiveStatus]);
    
    const closeComponents = () => {
        if(activeComponent.current === 'Video Player'){
            setVideoPlayerActiveStatus(1);
        }
        if(activeComponent.current === 'Engine'){
            setEngineActiveStatus(1);
        }
        if(activeComponent.current === 'Chat'){
            setChatRoomActiveStatus(1);
        }
        console.log("Setting Status: " + 1);
    }

    return (
        <> 
            {closeOptional && (
                <div className='close-components-button'
                    onClick={() => closeComponents()}
                    >
                    <div className='close-components-button-image'></div>
                </div>
            )}
        </>
    );
}

export default Close;