export default () => {

    let base = self; // eslint-disable-line no-restricted-globals
    
    let startTime = 0;
    let endTime = 0;
    
    let maximumQuadrantPriorityDistance = 2000;
    let maximumObjectPriorityDistance = 800; 

    // AI ENGINE REMOVE NEXT ENABLE FOLLOWING
    let maximumAvatarPriorityDistance = 10000; 
    //let maximumAvatarPriorityDistance = 800; 

    let objectQuadrants = [];
    
    let objects;
    let objectQuadrantsWidth;
    let objectQuadrantsHeight;
    let positionQuadrantX;
    let positionQuadrantY;
    let objectHoldersArray;
    let quadrantIndex;
    let closestQuadrantDistance;
    let quadrantDistance;
    let quadrantDeltaX;
    let quadrantDeltaY;
    
    let avatars;
    let npcReferenceData;
    let npcPhysicsData;
    let npcPriorityData;
    let priorityAvatarDeltaX;
    let priorityAvatarDeltaY;
    let priorityAvatarDistance;
    let priorityQuadrantDeltaX;
    let priorityQuadrantDeltaY;
    let priorityQuadrantDistance;
    let priorityObjectDeltaX;
    let priorityObjectDeltaY;
    let priorityObjectDistance;

    self.onmessage = (event) => { // eslint-disable-line no-restricted-globals
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Initialize"){
            initialize(event.payload);
        }
        if(event.type == "Update"){
            update(event.payload);
        }
    }
    
    const initialize = (eventData) => {
        startTime = new Date().getTime();
        objects = eventData.engineData.levelData.objects;
        
        objectQuadrantsWidth = eventData.mapWidth / eventData.mapWidthQuadrantCount;
        objectQuadrantsHeight = eventData.mapHeight / eventData.mapHeightQuadrantCount;
        
        for(let w = 0; w < eventData.mapWidthQuadrantCount; w++){
            for(let h = 0; h < eventData.mapHeightQuadrantCount; h++){
                positionQuadrantX = ((objectQuadrantsWidth * w) + objectQuadrantsWidth/2)
                positionQuadrantY = (objectQuadrantsHeight * h) + objectQuadrantsHeight/2;
                objectHoldersArray = [];
                objectQuadrants.push({x: positionQuadrantX, y: positionQuadrantY, objectHolders: objectHoldersArray});
            }
        }
        
        for(let o = 0; o < objects.length; o++){
            quadrantIndex = 0;
            closestQuadrantDistance = 10000000000;
            quadrantDistance = 0;
            for(let q = 0; q < objectQuadrants.length; q++){
                quadrantDeltaX = objectQuadrants[q].x - objects[o].position.x;
                quadrantDeltaY = objectQuadrants[q].y - objects[o].position.y;
                quadrantDistance = Math.sqrt(quadrantDeltaX * quadrantDeltaX + quadrantDeltaY * quadrantDeltaY);
                if(closestQuadrantDistance > quadrantDistance){
                    closestQuadrantDistance = quadrantDistance;
                    quadrantIndex = q;
                }
            }
            objectQuadrants[quadrantIndex].objectHolders.push(objects[o]);
        }
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Priority Clock: " + endTime);
        }
    }

    const update = (eventData) => {
        startTime = new Date().getTime();
        avatars = eventData.engineData.levelData.avatars;
        npcReferenceData = eventData.npcReferenceData;
        npcPhysicsData = eventData.npcPhysicsData;
        npcPriorityData = {
            objects: [],
            avatars: []
        }
        //console.log("PRIORITY AVATARS:");
        //console.log(avatars);
        for(let npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){
            npcPriorityData.avatars.push({indexes: []});
            for(let a = 0; a < avatars.length; a++){
                if(a !== npcIndex + 1){
                    priorityAvatarDeltaX = avatars[a].position.x - npcPhysicsData.destinations[npcIndex].x;
                    priorityAvatarDeltaY = avatars[a].position.y - npcPhysicsData.destinations[npcIndex].y;
                    priorityAvatarDistance = Math.sqrt(priorityAvatarDeltaX * priorityAvatarDeltaX + priorityAvatarDeltaY * priorityAvatarDeltaY);
                    //console.log("NPC Index: " + npcIndex + " && Avatar Index: " + a + " && DISTANCE: " + priorityAvatarDistance);
                    if(priorityAvatarDistance < maximumAvatarPriorityDistance){
                        //console.log("INDEXING---NPC Index: " + npcIndex + " && Avatar Index: " + a + " && DISTANCE: " + priorityAvatarDistance);
                        npcPriorityData.avatars[npcIndex].indexes.push(a);
                    }
                }
            }
            
            npcPriorityData.objects.push([]);
            
            for(let q = 0; q < objectQuadrants.length; q++){
                priorityQuadrantDeltaX = objectQuadrants[q].x - npcPhysicsData.destinations[npcIndex].x;
                priorityQuadrantDeltaY = objectQuadrants[q].y - npcPhysicsData.destinations[npcIndex].y;
                priorityQuadrantDistance = Math.sqrt(priorityQuadrantDeltaX * priorityQuadrantDeltaX + priorityQuadrantDeltaY * priorityQuadrantDeltaY);
                if(priorityQuadrantDistance < maximumQuadrantPriorityDistance){
                    for(let o = 0; o < objectQuadrants[q].objectHolders.Length; o++){
                        priorityObjectDeltaX = avatars[q].position.x - npcPhysicsData.destinations[q].x;
                        priorityObjectDeltaY = avatars[q].position.y - npcPhysicsData.destinations[q].y;
                        priorityObjectDistance = Math.sqrt(priorityObjectDeltaX * priorityObjectDeltaX + priorityObjectDeltaY * priorityObjectDeltaY);
                        if(priorityObjectDistance < maximumObjectPriorityDistance){
                            npcPriorityData.objects[npcIndex].push(objectQuadrants[q].objectHolders[o]);
                        }
                    }
                }
            }
        }
        
        //console.log("AVATAR INDEXES");
        //console.log(npcPriorityData.avatars);

        base.postMessage({type: "NPC Priority Data", payload: npcPriorityData });

        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Priority Clock: " + endTime);
        }
    }
}