import { Bob } from '../Concious/Bob/index.js';
import { Earl } from '../Concious/Earl/index.js';
import { Rob } from '../Concious/Rob/index.js';
import { Anne } from '../Concious/Anne/index.js';
import { Ohm } from '../Concious/Ohm/index.js';

const InfoMorph = (personality) => {
    
    let concious;
    
    if(personality == "bob"){
        concious = Bob;
    }
    if(personality == "earl"){
        concious = Earl;
    }
    if(personality == "rob"){
        concious = Rob;
    }
    if(personality == "anne"){
        concious = Anne;
    }
    if(personality == "ohm"){
        concious = Ohm;
    }
    
    return concious;
    console.log("Info Morphed Personaity")
}


export { InfoMorph };
