import React from 'react';
import { useGlobalState  } from '../globalcontext';

const Contact = () => {
    
    const { mobileActiveStatus, orientationActiveStatus } = useGlobalState();
    
    return (
        <div className="site-content">
            <div className="page-frame"
                style={{
                    top: mobileActiveStatus ? '32vh' : '26vh'
                }}
            >
                <div className="page-components">

                </div>
                <div className="page-content">
                    <p>CONTACT</p>
                    <p style={{ paddingTop: '3vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>EMAIL:</p>
                    <p style={{ paddingTop: '2vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>robert.wooden101@gmail.com</p>
                    <p style={{ paddingTop: '3vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>Phone:</p>
                    <p style={{ paddingTop: '2vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>1 (270) 267-0702</p>
                </div>
            </div>
        </div>
	);
};

export default Contact;
