import axios from 'axios';
import { apiUrl } from '../../config.js';

let playlistRecommended;

const RecommendGet = async (userId, type, count) => {
    return new Promise((resolve, reject) => {
        axios.get(`${apiUrl}/api/recommend/overall/${userId}?type=${type}&count=${count}`)
            .then(response => {
                console.log(userId);
                console.log(response.data);
                let playlist = [];
                if (response.data.length !== 0) {
                    playlist = response.data.map(item => ({
                        videoId: item.videoId,
                        title: item.title,
                        description: item.description,
                        thumbnail: item.thumbnail,
                        genre: item.genre,
                        director: item.director,
                        writer: item.writer,
                        cast: item.cast,
                        release: item.release,
                        rating: item.rating,
                        imdb: item.imdb,
                    }));
                }
                playlistRecommended = playlist;
                resolve(playlist);
            })
            .catch(error => {
                console.log('Error fetching search query:', error);
                reject(error);
            });
    });
};

export { RecommendGet, playlistRecommended};
