import React, { useState, useEffect, forwardRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import { apiUrl } from '../../config.js'

const Profiles = forwardRef(({chooseProfile}, ref) => {
    
    const { setUserId } = useGlobalState();
    
    const [creatingUser, setCreatingUser] = useState(false);
    const [userIdPrompt, setUserIdPrompt] = useState('');
    
    const [deletingUser, setDeletingUser] = useState(false);
    const [avatarUserId, setAvatarUserId] = useState('');
    
    const [settingUser, setSettingUser] = useState(false);

    const [avatarImages, setAvatarImages] = useState(null);

    const [avatarOptions, setAvatarOptions] = useState(null);

    const [profiles, setProfiles] = useState({
        userId: '',
        avatar: ''
    });
    
    const [profilesInitiated, setProfilesInitiated] = useState(false);
    
    useEffect(() => {
        importImages();
        loadProfiles();

    }, []);
    
    const importImages = async () => {
        
        const imageFiles = importAll(require.context('../../../public/images/video/profile', true, /\.(png|jpe?g|svg)$/, 'lazy'));

        let loadedImages = [];
        
        Promise.all(imageFiles).then((responses) => {
            console.log(responses);
            setAvatarImages(responses);
        });
        
    };
    
    const importAll = (r) => {
        return r.keys().map(r);
    }
    
    const loadProfiles = () => {
        const storedProfiles = JSON.parse(localStorage.getItem('profiles')) || [];
        setProfiles(storedProfiles);
        setProfilesInitiated(true);
        if(storedProfiles.length === 0){
            
        }
        console.log("Loaded Profiles");
    };

    const changeAvatarImage = (avatarImageUserId, newAvatarImage) => {
        const updatedProfiles = profiles.map(profile => {
            if (profile.userId === avatarImageUserId) {
                return { ...profile, avatar: newAvatarImage };
            }
            return profile;
        });

        setProfiles(updatedProfiles);
        localStorage.setItem('profiles', JSON.stringify(updatedProfiles));
        setSettingUser(false);
    };

    const createProfile = (userId) => {
        const avatarImageUrl = '';
        const newProfile = { 
            userId: userId,
            avatar: avatarImageUrl 
        };
        const updatedProfiles = [...profiles, newProfile];

        setProfiles(updatedProfiles);
        localStorage.setItem('profiles', JSON.stringify(updatedProfiles));
        setAvatarUserId(userId);
        setAvatarOptions(true);
    };
    
    const deleteProfile = (oldUserId) => {
        const updatedProfiles = profiles.filter(profile => profile.userId !== oldUserId);

        setProfiles(updatedProfiles);
        localStorage.setItem('profiles', JSON.stringify(updatedProfiles));
    };

    return (
        <div className="video-player-profiles">
            <div className="video-player-profiles-background"></div>
            <>
                {creatingUser && (
                    <div className="video-player-profiles-create-user-display">
                        <div className="video-player-profiles-create-user-confirm-button" onClick={() => {createProfile(userIdPrompt); setCreatingUser(false);}}>
                            <div className="video-player-profiles-create-user-confirm-button-image" ></div>
                        </div>
                        <div className="video-player-profiles-create-user-cancel-button" onClick={() => setCreatingUser(false)} >
                            <div className="video-player-profiles-create-user-cancel-button-image"></div>
                        </div>
                        <input className="video-player-profiles-create-user-input"
                            type="text"
                            value={userIdPrompt}
                            onChange={(e) => setUserIdPrompt(e.target.value)}
                            placeholder="Enter User Id" 
                        />
                    </div>
                )}
            </>
            <>
                {deletingUser && (
                    <div className="video-player-profiles-delete-user-display">
                        <div className="video-player-profiles-delete-user-text">Delete?</div>
                        <div className="video-player-profiles-delete-user-confirm-button" onClick={() => {deleteProfile(avatarUserId); setDeletingUser(false);}} >
                            <div className="video-player-profiles-delete-user-confirm-button-image"></div>
                        </div>
                        <div className="video-player-profiles-delete-user-cancel-button" onClick={() => setDeletingUser(false)} >
                            <div className="video-player-profiles-delete-user-cancel-button-image"></div>
                        </div>
                    </div>
                )}
            </>
            <>
                {avatarOptions && (
                    <div className="video-player-profiles-avatar-image-display">
                        {avatarImages.map((avatarImage) => (
                            <div key={avatarImage}
                                className="video-player-profiles-avatar-image-item"
                                onClick={() => {setAvatarOptions(false); changeAvatarImage(avatarUserId, avatarImage)}}
                            >
                                {console.log(avatarImage)}
                                <img className="video-player-profiles-avatar-image-item-image" src={avatarImage} />
                            </div>
                        ))}
                    </div>
                )}
            </>
            <div className="video-player-profiles-create-user-button" onClick={() => setCreatingUser(true)} >
                <div className="video-player-profiles-create-user-button-image"></div>
            </div>
            <div className="video-player-profiles-list">
                {profilesInitiated && (
                    <>
                        {(profiles.length === 0) ? (
                                <p className="video-player-profiles-text">Add New User</p>
                            ) : (
                                <>
                                    {profiles.map(profile => (

                                        <div key={profile.userId}
                                            className="video-player-profiles-item-container"
                                        >
                                            <div className="video-player-profiles-item-button" onClick={() => {chooseProfile(profile.userId); setUserId(profile.userId);}} >
                                                <img className="video-player-profiles-item-avatar" src={profile.avatar} />
                                                <div className="video-player-profiles-item-text">{profile.userId}</div>
                                            </div>
                                            <div className="video-player-profiles-item-settings-button" onClick={() => {setSettingUser(true); setAvatarUserId(profile.userId)}} >
                                                <div className="video-player-profiles-item-settings-button-image"></div>
                                            </div>
                                            {settingUser && profile.userId === avatarUserId && (
                                                <>
                                                    <div className="video-player-profiles-item-avatar-settings-close-button" onClick={() => setSettingUser(false)} >
                                                    </div>
                                                    <div className="video-player-profiles-item-avatar-settings-container" >
                                                        <div className="video-player-profiles-item-avatar-button" onClick={() => setAvatarOptions(true)} >
                                                            <div className="video-player-profiles-item-avatar-button-image"></div>
                                                        </div>
                                                        <div className="video-player-profiles-item-delete-button" onClick={() => {setDeletingUser(true); setAvatarUserId(profile.userId)}} >
                                                            <div className="video-player-profiles-item-delete-button-image"></div>
                                                        </div>
                                                    </div>
                                                </>
                                            )}
                                        </div>
                                    ))}
                                </>
                            )
                        }
                    </>
                )}
            </div>
        </div>
    );
});

export default Profiles;