export default () => {

    let base = self; // eslint-disable-line no-restricted-globals

    let startTime = 0;
    let endTime = 0;
    
    let windowData;
    let playerPhysicsData;
    let playerAnimationData;
    let gameReferenceData;

    let mouseX;
    let mouseY;

    let centerX;
    let centerY;

    let relativeX;
    let relativeY;

    let oldPlayerAnimationAngle;
    let oldPlayerAnimationFrame;

    let newPlayerAnimationAngle;
    let newPlayerAnimationFrame;
    let bloomAngleZeroOffsetY;
    let bloomAngleZeroOffsetX;
    let bloomAngleOneOffsetY;
    let bloomAngleOneOffsetX;
    let bloomAngleTwoOffsetY;
    let bloomAngleTwoOffsetX;
    let bloomAngleThreeOffsetY;
    let bloomAngleThreeOffsetX;
    let bloomAngleFourOffsetY;
    let bloomAngleFourOffsetX;
    let bloomAngleFiveOffsetY;
    let bloomAngleFiveOffsetX;
    let bloomAngleSixOffsetY;
    let bloomAngleSixOffsetX;
    let bloomAngleSevenOffsetY;
    let bloomAngleSevenOffsetX;

    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (eventData) => {
        if(eventData.type == "Update"){
            update(eventData.payload);
        }
    }

    /* Movement - Collision */
    const update = (eventData) => {
        startTime = new Date().getTime();
        
        windowData = eventData.windowData;
        playerPhysicsData = eventData.playerPhysicsData;
        playerAnimationData = eventData.playerAnimationData;
        gameReferenceData = eventData.gameReferenceData;
        
        mouseX = eventData.mouseState.mousePosition.x;
        mouseY = eventData.mouseState.mousePosition.y;

        centerX = windowData.windowHalfWidth + playerPhysicsData.avatarOffsetX;
        centerY = windowData.windowHalfHeight + playerPhysicsData.avatarOffsetY;
        
        relativeX = mouseX - centerX;
        relativeY = mouseY - centerY;

        oldPlayerAnimationAngle = playerAnimationData.playerAnimationAngle;
        oldPlayerAnimationFrame = playerAnimationData.playerAnimationFrame;
        
        newPlayerAnimationAngle = 0;
        newPlayerAnimationFrame = oldPlayerAnimationFrame;
        
        /* Sprite Animation Angle */
        if(relativeX < 30 && relativeX > -30 && relativeY > 30){
            newPlayerAnimationAngle = 0;
        }
        if(relativeX > 30 && relativeY > 30){
            newPlayerAnimationAngle = 1;
        }
        if(relativeX > 30 && relativeY < 30 && relativeY > -30){
            newPlayerAnimationAngle = 2;
        }
        if(relativeX > 30 && relativeY < -30){
            newPlayerAnimationAngle = 3;
        }
        if(relativeX < 30 && relativeX > -30 && relativeY < -30){
            newPlayerAnimationAngle = 4;
        }
        if(relativeX < -30 && relativeY < -30){
            newPlayerAnimationAngle = 5;
        }
        if(relativeX < -30 && relativeY < 30 && relativeY > -30){
            newPlayerAnimationAngle = 6;
        }
        if(relativeX < -30 && relativeY > 30){
            newPlayerAnimationAngle = 7;
        }
        
        /* Fresnel */
        
        if(oldPlayerAnimationAngle != newPlayerAnimationAngle){
            playerAnimationData.playerAnimationAngle = newPlayerAnimationAngle;
            
            if(playerAnimationData.playerAnimationAngle == 0){
                playerAnimationData.playerFresnelLeft = 2;
                playerAnimationData.playerFresnelTop = -2;
            }
            if(playerAnimationData.playerAnimationAngle == 1){
                playerAnimationData.playerFresnelLeft = 2;
                playerAnimationData.playerFresnelTop = -1;
            }
            if(playerAnimationData.playerAnimationAngle == 2){
                playerAnimationData.playerFresnelLeft = 2;
                playerAnimationData.playerFresnelTop = -4;
            }
            if(playerAnimationData.playerAnimationAngle == 3){
                playerAnimationData.playerFresnelLeft = -4;
                playerAnimationData.playerFresnelTop = -3;
            }
            if(playerAnimationData.playerAnimationAngle == 4){
                playerAnimationData.playerFresnelLeft = -3;
                playerAnimationData.playerFresnelTop = -1;
            }
            if(playerAnimationData.playerAnimationAngle == 5){
                playerAnimationData.playerFresnelLeft = -2;
                playerAnimationData.playerFresnelTop = -4;
            }
            if(playerAnimationData.playerAnimationAngle == 6){
                playerAnimationData.playerFresnelLeft = 2;
                playerAnimationData.playerFresnelTop = -2;
            }
            if(playerAnimationData.playerAnimationAngle == 7){
                playerAnimationData.playerFresnelLeft = 3;
                playerAnimationData.playerFresnelTop = -1;
            }
        
            /* Bloom */ 
            
            if(playerAnimationData.playerAnimationAngle == 0){
                bloomAngleZeroOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50);
                bloomAngleZeroOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 7.75); 
                playerAnimationData.playerBloomInnerTop = bloomAngleZeroOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleZeroOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleZeroOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleZeroOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleZeroOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleZeroOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 7.75);         
            }

            if(playerAnimationData.playerAnimationAngle == 1){
                bloomAngleOneOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75);
                bloomAngleOneOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 5.75);
                playerAnimationData.playerBloomInnerTop = bloomAngleOneOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleOneOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleOneOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleOneOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleOneOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleOneOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 5.75);
            }

            if(playerAnimationData.playerAnimationAngle == 2){
                bloomAngleTwoOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75);
                bloomAngleTwoOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 3.00);
                playerAnimationData.playerBloomInnerTop = bloomAngleTwoOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleTwoOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleTwoOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleTwoOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleTwoOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleTwoOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 3.00); 
            }

            if(playerAnimationData.playerAnimationAngle == 3){
                bloomAngleThreeOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75);
                bloomAngleThreeOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -3.00);
                playerAnimationData.playerBloomInnerTop = bloomAngleThreeOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleThreeOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleThreeOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleThreeOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleThreeOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleThreeOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight  - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -3.00);
            }

            if(playerAnimationData.playerAnimationAngle == 4){
                bloomAngleFourOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50);
                bloomAngleFourOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -5.00);
                playerAnimationData.playerBloomInnerTop = bloomAngleFourOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleFourOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleFourOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleFourOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleFourOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleFourOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -5.00);
            }

            if(playerAnimationData.playerAnimationAngle == 5){
                bloomAngleFiveOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50);
                bloomAngleFiveOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -1.50);
                playerAnimationData.playerBloomInnerTop = bloomAngleFiveOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleFiveOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleFiveOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleFiveOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleFiveOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleFiveOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.50) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * -1.50);
            }

            if(playerAnimationData.playerAnimationAngle == 6){
                bloomAngleSixOffsetY = windowData.windowHalfHeight - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 5.00);
                bloomAngleSixOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 4.25);
                playerAnimationData.playerBloomInnerTop = bloomAngleSixOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleSixOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleSixOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleSixOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleSixOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleSixOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight  - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 5.00) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 4.25);
            }

            if(playerAnimationData.playerAnimationAngle == 7){
                bloomAngleSevenOffsetY = windowData.windowHalfHeight  - playerAnimationData.playerBloomInnerHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75);
                bloomAngleSevenOffsetX = windowData.windowHalfWidth - playerAnimationData.playerBloomInnerWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 6.75);
                playerAnimationData.playerBloomInnerTop = bloomAngleSevenOffsetY;
                playerAnimationData.playerBloomInnerLeft = bloomAngleSevenOffsetX;
                playerAnimationData.playerBloomMiddleTop = bloomAngleSevenOffsetY;
                playerAnimationData.playerBloomMiddleLeft = bloomAngleSevenOffsetX;
                playerAnimationData.playerBloomOutterTop = bloomAngleSevenOffsetY;
                playerAnimationData.playerBloomOutterLeft = bloomAngleSevenOffsetX;

                playerAnimationData.playerShadowmapTop = windowData.windowHalfHeight  - playerAnimationData.playerShadowmapHeight/2 - ((playerAnimationData.playerContainerHeight/20) * 4.75) + playerAnimationData.playerContainerHeight;
                playerAnimationData.playerShadowmapLeft = windowData.windowHalfWidth - playerAnimationData.playerShadowmapWidth/2 + ((playerAnimationData.playerContainerWidth/20) * 6.75);
            }            

            base.postMessage({type: "Player Animation Data", payload: playerAnimationData});
        
        }
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Animation Clock: " + endTime);
        }
    }
}