import React, { useState, useLayoutEffect, useRef } from 'react';
import { useGlobalState  } from '../globalcontext';
import { doublePass } from '../config.js'

const Video = () => {
    
    const { mobileActiveStatus, engineActiveStatus, videoPlayerActiveStatus, chatRoomActiveStatus, setVideoPlayerActiveStatus } = useGlobalState();

    const initializedStart = useRef(false);
    
    useLayoutEffect(() => {
        if(initializedStart.current === doublePass){ 
            setVideoPlayerActiveStatus(1);
            console.log("Set Video Active: " + 1);
        }
        
        return () => {
            if(initializedStart.current === doublePass){
                setVideoPlayerActiveStatus(0);
                console.log("Set Video Active: " + 0);
            }
            initializedStart.current = true;
        };
    }, []);
    
    const setStatus = (value) => {
        setVideoPlayerActiveStatus(value);
        console.log("Set Video Active: " + value);
    }
    
	return (
        <div className="site-content" >
            <div className="page-frame"
                style={{
                    overflow: 'hidden',
                    top: mobileActiveStatus ? '32vh' : '26vh'
                }}
            >
                <div className="page-components">

                </div>
                <div className="page-content" style={{overflow: 'hidden'}}>
                    {(engineActiveStatus !== 3 && chatRoomActiveStatus !== 3 && videoPlayerActiveStatus !== 3 && engineActiveStatus !== 2 && chatRoomActiveStatus !== 2 && videoPlayerActiveStatus !== 2) && (
                        <div className="video-player-start-button" >
                            <div className="video-player-start-button-glow" style={{ animation: 'rotateLeft 2s linear infinite', opacity: '.5' }}></div>
                            <div className="video-player-start-button-glow" style={{ animation: 'rotateRight 2s linear infinite', opacity: '.5' }}></div>
                            <div className="video-player-start-button-play" onClick={() => setStatus(2)} >
                                <div className="video-player-start-button-play-letter" style={{ top: '15%', left: '30%'}}>P</div>
                                <div className="video-player-start-button-play-letter" style={{ top: '40%', left: '65%'}}>Y</div>
                                <div className="video-player-start-button-play-letter" style={{ top: '55%', left: '40%'}}>A</div>
                                <div className="video-player-start-button-play-letter" style={{ top: '50%', left: '10%'}}>L</div>
                                <div className="video-player-start-button-play-letter-mini" style={{ top: '25%', left: '50%'}}>A</div>
                                <div className="video-player-start-button-play-letter-mini" style={{ top: '50%', left: '60%'}}>L</div>
                                <div className="video-player-start-button-play-letter-mini" style={{ top: '60%', left: '50%'}}>P</div>
                                <div className="video-player-start-button-play-letter-mini" style={{ top: '50%', left: '25%'}}>Y</div>
                            </div>
                        </div>
                    )}
                    <p>STREAM</p>
                    <p style={{ paddingTop: '4vh'}}></p>
                </div>
            </div>
        </div>
	);
};

export default Video;