export default () => {
      
    const base = self; // eslint-disable-line no-restricted-globals
    
    let debug = false;
    let startTime = 0;
    let endTime = 0;
    
    // 23  13
    const topDistanceRatioX = 18;
    const topDistanceRatioY = 10;

    let angle = 0;

    const maximumRenderDistance = 1200;

    let deviationX = 0;
    let deviationY = 0;

    let gameObjects;   

    let objectTypeParameters;
    
    let playerPositionX;
    let playerPositionY;
    
    let object;
    let objectDistanceX;
    let objectDistanceY;
    let objectPositionX;
    let objectPositionY;
    let objectType;
    let distanceX;
    let distanceY;
   
    base.onmessage = (event) => { 
        processMessage(event.data);
    }

    const processMessage = (eventData) => {
        if(eventData.type == "Update"){
            update(eventData);
        }
    }
    
    /* Objects */
    const update = (eventData) => {
        startTime = new Date().getTime();

        debug = eventData.payload.debug;
        playerPositionX = eventData.payload.playerOffsetX + eventData.payload.screenWidth/2;
        playerPositionY = eventData.payload.playerOffsetY + eventData.payload.screenHeight/2;
     
        gameObjects = eventData.payload.gameObjects;

        objectTypeParameters = eventData.payload.objectTypeParameters;
        
        
        for(var i = 0; i < gameObjects.length/2; i++){
            object = gameObjects[i * 2];
            
            objectDistanceX = object.position.x - playerPositionX;
            if(objectDistanceX < maximumRenderDistance && objectDistanceX > -maximumRenderDistance){
                objectDistanceY = object.position.y - playerPositionY;
                if(objectDistanceY < maximumRenderDistance && objectDistanceY > -maximumRenderDistance){
                    objectPositionX = object.position.x;
                    objectPositionY = object.position.y;
                    angle = Math.atan2(objectPositionY - playerPositionY, objectPositionX - playerPositionX);
                    
                    objectType = null;
                    for(var t = 0; t < objectTypeParameters.length; t++){
                        if(object.type.includes(objectTypeParameters[t].type)){
                            objectType = objectTypeParameters[t];
                        }
                    }
                    
                    if(object.visible){
                        distanceX = Math.abs((playerPositionX - (object.position.x + objectType.weight.x)) / topDistanceRatioX) + 2; 
                        distanceY = Math.abs((playerPositionY - (object.position.y + objectType.weight.y)) / topDistanceRatioY) + (distanceX/2);
                        if(distanceX > objectType.maxDeviation.x){
                            distanceX = objectType.maxDeviation.x;
                        }
                        if(distanceY > objectType.maxDeviation.y){
                            distanceY = objectType.maxDeviation.y;
                        }
                        deviationX = objectPositionX - Math.cos(angle) * distanceX * objectType.perspectiveDeviation.x;
                        deviationY = objectPositionY - Math.sin(angle) * distanceY * objectType.perspectiveDeviation.y;
                        if(objectType.inverted == true){
                            gameObjects[i * 2].renderPosition = {x: deviationX, y: deviationY};
                            gameObjects[i * 2].class = object.type + '-bottom';
                            gameObjects[i * 2 + 1].renderPosition = {x: objectPositionX, y: objectPositionY};
                            gameObjects[i * 2 + 1].class = object.type + '-top';
                        }
                        if(objectType.inverted == false){
                            gameObjects[i * 2].renderPosition = {x: objectPositionX, y: objectPositionY};
                            gameObjects[i * 2].class = object.type + '-bottom';
                            gameObjects[i * 2 + 1].renderPosition = {x: deviationX, y: deviationY};
                            gameObjects[i * 2 + 1].class = object.type + '-top';
                        }
                        object.visible = false;
                    }else{
                        distanceX = Math.abs((playerPositionX - (object.position.x + objectType.weight.x)) / topDistanceRatioX) + 2; 
                        distanceY = Math.abs((playerPositionY - (object.position.y + objectType.weight.y)) / topDistanceRatioY) + (distanceX/2);
                        if(distanceX > objectType.maxDeviation.x){
                            distanceX = objectType.maxDeviation.x;
                        }
                        if(distanceY > objectType.maxDeviation.y){
                            distanceY = objectType.maxDeviation.y;
                        }
                        deviationX = objectPositionX - Math.cos(angle) * distanceX * objectType.perspectiveDeviation.x;
                        deviationY = objectPositionY - Math.sin(angle) * distanceY * objectType.perspectiveDeviation.y;
                        if(objectType.inverted == true){
                            gameObjects[i * 2].renderPosition = {x: deviationX, y: deviationY};
                            gameObjects[i * 2].class = object.type + '-bottom-dim';
                            gameObjects[i * 2 + 1].renderPosition = {x: objectPositionX, y: objectPositionY};
                            gameObjects[i * 2 + 1].class = object.type + '-top';
                        }
                        if(objectType.inverted == false){
                            gameObjects[i * 2].renderPosition = {x: objectPositionX, y: objectPositionY};
                            gameObjects[i * 2].class = object.type + '-bottom-dim';
                            gameObjects[i * 2 + 1].renderPosition = {x: deviationX, y: deviationY};
                            gameObjects[i * 2 + 1].class = object.type + '-top';
                        }
                    }
                }
            }
        } 


        base.postMessage({type: "Game Object Data", gameObjects: gameObjects});

        if(debug){
            endTime = new Date().getTime() - startTime;
            console.log("Object Clock: " + endTime);
        }
    }
}