import React, { useState, useEffect, useRef, forwardRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import { apiUrl } from '../../config.js'
import axios from 'axios';
import { RecommendGet } from '../RecommendGet';
import { RecommendSet } from '../RecommendSet';

const Recommendations = forwardRef(({ openStream, setRecommending, userId }, ref) => {
    
    const { setSubMenuActiveStatus } = useGlobalState();

    const [loaded, setLoaded] = useState(false);
    
    const [loadingText, setLoadingText] = useState('Loading');
    
    const [playlist, setPlaylist] = useState(null);
    
    const playlistReference = useRef(null);
    
    useEffect(() => {
        getRecommend(userId);
    }, [])
        
    const getRecommend = async (userId) => {
        playlistReference.current = await RecommendGet(userId, 'genre', 10); 
        console.log(playlistReference.current);
        setPlaylist(playlistReference.current);
        setLoadingText('');
        setLoaded(true);
        console.log("Received playlist");
    };
    
    const setRecommend = async (videoData, userId) => {
        RecommendSet(videoData, userId); 
    };

    return (
        <div className="video-player-recommendations">
            <div className="video-player-recommendations-display">
                {loaded ? (
                        <>
                            {playlist.map((video, index) => (
                                <div key={index}
                                    className="video-player-recommendations-item"
                                    onClick={() => {openStream('0', video.videoId, true); setRecommend(video, userId); setRecommending(false);}}
                                >
                                    <img className="video-player-recommendations-item-image" src={video.thumbnail.medium.url} alt={video.title} />
                                </div>
                            ))}
                        </>
                    ) : (
                        <div className="video-player-recommendations-loading-text">{loadingText}</div>
                    )
                }
            </div>
            <div className="video-player-recommendations-suggestion-text">Suggested for you:</div>
        </div>
    );
});

export default Recommendations;