import axios from 'axios';
import { apiUrl } from '../../config.js'

const RecommendSet = async (videoData, userId) => {
    

    let formData = {
        userId: userId,
        recommendationData: {
            genre: videoData.genre.split(',').map(item => item.trim()),
            director: videoData.director.split(',').map(item => item.trim()),
        },
    };
    
    axios.post(`${apiUrl}/api/recommend/set/`, formData)
        .then(async (response) => {
            console.log(formData);
            console.log(response.data);
        })
        .catch((error) => console.error('Error fetching search query:', error));
    
    
}


export { RecommendSet };
