export default () => {

    let base = self; // eslint-disable-line no-restricted-globals
    
    let startTime;
    let endTime;
    
    const npcWaypointData = [];
    
    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Initialize"){
            initialize(event.payload);
        }
        if(event.type == "Update"){
            update(event.payload);
        }
    }
    
    let npcReferenceData;
    let npcPriorityData;
    let npcPhysicsData;
    let npcAnimationData;
    let avatars;
    let npcContainer;
    let colliding;
    let deltaX;
    let deltaY;
    let distance;
    let npcInteractiveDistance;
    let deltaAvatarX;
    let deltaAvatarY;
    let distanceAvatar;
    
    const initialize = (eventData) => {
        npcReferenceData = eventData.npcReferenceData;
        
        for(let npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){
            npcWaypointData.push({current: new Date().getTime(), previous: new Date().getTime(), waypointIndex: 0});
        }
    }
    
    /* Movement - Collision - Animation */
    const update = (eventData) => {
        startTime = new Date().getTime();
        npcReferenceData = eventData.npcReferenceData;
        npcPriorityData = eventData.npcPriorityData;
        npcPhysicsData = eventData.npcPhysicsData;
        npcAnimationData = eventData.npcAnimationData;
        avatars = eventData.engineData.levelData.avatars;
        
        //console.log("PHYSICS WORKER AVATARS:");
        //console.log(avatars);
        //console.log("PHYSICS WORKER INDEXES:");
        //console.log(npcPriorityData.avatars);
        
        for(let npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){
            
            if(npcAnimationData.animations[npcIndex].animationType == "stand" || npcAnimationData.animations[npcIndex].animationType == "walk" || npcAnimationData.animations[npcIndex].animationType == "run"){
                npcContainer = npcReferenceData.npcContainers[npcIndex];
                npcWaypointData[npcIndex].current = new Date().getTime();
                if(npcWaypointData[npcIndex].current - npcWaypointData[npcIndex].previous > npcContainer.updateWaypointTime){
                    npcWaypointData[npcIndex].previous = npcWaypointData[npcIndex].current;
                    
                    // AI ENGINE LINEAR PATHING
                    npcWaypointData[npcIndex].waypointIndex += 1;
                    if(npcWaypointData[npcIndex].waypointIndex >= npcContainer.waypoints.length){
                        npcWaypointData[npcIndex].waypointIndex = 0;
                    }
                    //console.log(npcWaypointData[npcIndex].waypointIndex);
                    
                    // AI ENGINE RANDOM ROAM
                    /*
                    npcWaypointData[npcIndex].waypointIndex = Math.floor(Math.random() * npcContainer.waypoints.length);
                    console.log(npcWaypointData[npcIndex].waypointIndex);
                    */
                }
                
                
                colliding = false;
                for(let o = 0; o < npcPriorityData.objects[npcIndex].length; o++){
                    deltaX = npcContainer.position.x - npcPriorityData.objects[o].position.x;
                    deltaY = npcContainer.position.y - npcPriorityData.objects[o].position.y;
                    distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
                    npcInteractiveDistance = npcContainer.interactiveDistance;
                    if(distance < npcInteractiveDistance){
                        colliding = true;
                    }
                } 
                
                //console.log(npcPriorityData.avatars[npcIndex].indexes.length);
                for(let a = 0; a < npcPriorityData.avatars[npcIndex].indexes.length; a++){
                    deltaAvatarX = npcContainer.position.x - avatars[npcPriorityData.avatars[npcIndex].indexes[a]].position.x;
                    deltaAvatarY = npcContainer.position.y - avatars[npcPriorityData.avatars[npcIndex].indexes[a]].position.y;
                    distanceAvatar = Math.sqrt(deltaAvatarX * deltaAvatarX + deltaAvatarY * deltaAvatarY);
                    npcInteractiveDistance = npcContainer.interactiveDistance;
                    //console.log(distanceAvatar);
                    if(distanceAvatar > npcInteractiveDistance){
                        npcPhysicsData.interactions[npcIndex] = false;
                    }
                    if(distanceAvatar < npcInteractiveDistance){
                        //console.log("INTERACTING----NPC INDEX: " + npcIndex + " && AVATAR INDEX: " + npcPriorityData.avatars[npcIndex].indexes[a] + " DISTANCE: " + distanceAvatar);
                        npcPhysicsData.interactions[npcIndex] = true;
                    }
                } 
                
                // AI ENGINE REMOVE NEXT ENABLE FOLLOWING
                if(!colliding){
                //if(!colliding && !npcPhysicsData.interactions[npcIndex]){
                    npcPhysicsData.destinations[npcIndex].x = npcContainer.waypoints[npcWaypointData[npcIndex].waypointIndex].x;
                    npcPhysicsData.destinations[npcIndex].y = npcContainer.waypoints[npcWaypointData[npcIndex].waypointIndex].y;
                }
                
                // AI ENGINE REMOVE NEXT ENABLE FOLLOWING
                if(colliding){
                //if(colliding || npcPhysicsData.interactions[npcIndex]){
                    npcPhysicsData.destinations[npcIndex].x = npcPhysicsData.absolutes[npcIndex].x;
                    npcPhysicsData.destinations[npcIndex].y = npcPhysicsData.absolutes[npcIndex].y;
                }
            }
            
            // AI ENGINE REMOVE NEXT
            npcPhysicsData.interactions[npcIndex] = eventData.staticInteraction;
        }

        const serializedData = {
            destinations: npcPhysicsData.destinations,
            interactions: npcPhysicsData.interactions,
        }
        
        base.postMessage({type: "NPC Physics Data", payload: serializedData});
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Physics Clock: " + endTime);
        }
    }
}