import React, { useState, useEffect } from 'react';
import { useGlobalState  } from '../globalcontext';
import CrystalLightning from '../components/CrystalLightning';

const Home = () => {
    
    const { mobileActiveStatus } = useGlobalState();
    
    return (
        <div className="site-content" >
            <div className="page-frame"
                style={{
                    top: mobileActiveStatus ? '32vh' : '26vh'
                }}
            >
                <div className="page-components">
                    <CrystalLightning alt="" />
                </div>
                <div className="page-content">
                    <p></p>
                </div>
            </div>
        </div>
    );
};

export default Home;