import React, { Component, useState, useLayoutEffect, useRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import WebWorker from '../../WebWorker';
import objectWorker from "../Worker/objectmanager.worker.js";
import { Objects } from '../Objects/index.js';
import { ObjectTypeParameters } from '../ObjectTypeParameters/index.js'
import { doublePass } from '../../config.js'

const ObjectManager = ({props}) => {
    
    const { engineActiveStatus } = useGlobalState();
    const engineActiveStatusReference = useRef();

    const objects = useRef(props.engineData.current.levelData.objects);
    
    const gameObjects = useRef([]);
    
    const gameObjectsReferences = useRef([]);

    const gameObjectWorker = useRef(null);

    const initialized = useRef(false);

    const initialStart = useRef(false);
    
    const timers = useRef([
        { name: 'Object Timer', duration: 271, value: 0 },
    ]);
    
    const startTime = useRef(0);
    const endTime = useRef(0);
    
    useLayoutEffect(() => {
        engineActiveStatusReference.current = engineActiveStatus;
    }, [engineActiveStatus]);
    
    useLayoutEffect(() => {
        
        const interval = setInterval(() => {
            timers.current.forEach(timer => {
                timer.value += 10;
                if(timer.value >= timer.duration){
                    timer.value = 0;
                    processTimer(timer.name);
                }
            });
        }, 10);

        if(initialStart.current === doublePass){
            gameObjectWorker.current = new WebWorker(objectWorker);

            gameObjectWorker.current.onmessage = (event) => {
                update(event.data);
            };

            initialize();
            
            initialized.current = true;
        }
                
        return () => {
            clearInterval(interval);
            if(initialStart.current === doublePass){
                gameObjectWorker.current.terminate();
            }
            initialStart.current = true;
        
        };
    }, []); 
    
    const serializedProps = useRef(null);
    
    const processTimer = (timerName) => { 
        startTime.current = new Date().getTime();
        
        serializedProps.current = {
            debug: props.engineData.current.debug,
            playerOffsetY: -parseInt(props.references.gameLevelContainerReference.current.style.top, 10),
            playerOffsetX: -parseInt(props.references.gameLevelContainerReference.current.style.left, 10),
            playerWidth: parseInt(props.references.gamePlayerContainerReference.current.style.width),
            playerHeight: parseInt(props.references.gamePlayerContainerReference.current.style.height),
            screenWidth: window.innerWidth,
            screenHeight: window.innerHeight,
            gameObjects: gameObjects.current,
            objectTypeParameters: ObjectTypeParameters,
            
        };
        if(timerName === "Object Timer" && engineActiveStatusReference.current === 3){
            //gameObjectWorker.current.postMessage({type: "Update", payload: serializedProps.current });
        };
        
        if(props.engineData.current.debug){
            endTime.current = new Date().getTime() - startTime.current;
            console.log("Object Clock: " + endTime.current);
        }
    }
    
    const objectCopyOne = useRef(null);
    const objectCopyTwo = useRef(null);
    
    const initialize = () =>{
        for(var x = 0; x < objects.current.length; x++){            
            objectCopyOne.current = JSON.parse(JSON.stringify(objects.current[x]));
            gameObjects.current.push(objectCopyOne.current);
            objectCopyTwo.current = JSON.parse(JSON.stringify(objects.current[x]));
            gameObjects.current.push(objectCopyTwo.current);
        }
    }
    
    const gameObjectsData = useRef(null);
    
    const update = (eventData) =>{
        gameObjectsData.current = eventData.gameObjects;
        for(var x = 0; x < gameObjectsData.current.length; x++){
            gameObjectsReferences.current[x].style.top = gameObjectsData.current[x].renderPosition.y + "px";
            gameObjectsReferences.current[x].style.left = gameObjectsData.current[x].renderPosition.x + "px";
            gameObjectsReferences.current[x].className = gameObjectsData.current[x].class;
          
        }
    }
    
    
    return (
        <div style={{top: '0px', left: '0px', width: '100%', height: '100%', position: 'relative'}}>
            {objects.current.map((item, index) => (
                <>
                    <div
                        key={index * 2}
                        ref={(ref) => (gameObjectsReferences.current[index * 2] = ref)}
                        className='object'
                        style={{
                            position: 'absolute',
                            left: `${item.renderPosition.x}px`,
                            top: `${item.renderPosition.y}px`,
                            width: `${item.width}px`,
                            height: `${item.height}px`,
                            backgroundSize: 'cover',
                        }}
                    />
                    <div
                        key={index * 2 + 1}
                        ref={(ref) => (gameObjectsReferences.current[index * 2 + 1] = ref)}
                        className='object'
                        style={{
                            position: 'absolute',
                            left: `${item.renderPosition.x}px`,
                            top: `${item.renderPosition.y}px`,
                            width: `${item.width}px`,
                            height: `${item.height}px`,
                            backgroundSize: 'cover',
                        }}
                    />
                </>
            ))}
        </div>
    );
}

export default ObjectManager;