import React, { useState, useLayoutEffect, useRef } from 'react';
import axios from 'axios';
import { useGlobalState  } from '../../globalcontext';
import VideoIFramePlayer from '../VideoIFramePlayer';
import { VideoPlayerModuleLoader } from '../VideoPlayerModuleLoader';
import { apiUrl, doublePass } from '../../config.js'
import WebWorker from '../../WebWorker';
import videoPlayerWorker from '../Worker/videoplayer.worker.js';
import SwipeDetector from '../SwipeDetector';
import SearchBar from '../SearchBar';
import Profiles from '../Profiles';
import { RecommendGet } from '../RecommendGet';
import { RecommendSet } from '../RecommendSet';
import { GetVideosByGenre } from '../GetVideosByGenre';

//UnModulated for your inconvenience

const VideoPlayer = () => {    

    const userId = useRef('null');
    const [choosingProfile, setChoosingProfile] = useState(true);
    
    const debug = useRef(false);
    const startTime = useRef(null);
    const endTime = useRef(null);

    const { orientationActiveStatus, mobileActiveStatus, videoPlayerActiveStatus, setVideoPlayerActiveStatus, setSubMenuActiveStatus, artificialIntelligenceActiveStatus, setArtificialIntelligenceActiveStatus, openStreamGlobal } = useGlobalState();
    const videoPlayerActiveStatusReference = useRef(0);
    const mobileActiveStatusReference = useRef(0);
    
    const videoGlowScales = useRef([.001, 1.4, 300]);
    const videoPlayerScaffoldScales = useRef([.0001, .0001, 1]);
    const videoPlayerFrameScales = useRef([.0001, .0001, 1]);

    const videoPlayerScaffoldCurrentScale = useRef(.01);
    const videoPlayerFrameCurrentScale = useRef(.01);
    
    const transitionSpeed = useRef(1);
    
    const glowImageSize = useRef(.001);
    const scaffoldImageSize = useRef(.001);
    const frameImageSize = useRef(.001);
    
    const glowReference = useRef(null);
    const scaffoldReference = useRef(null);
    const frameReference = useRef(null);
    
    const lerpSpeedDurationFrame = useRef(500);    
        
    const timestampFrame = useRef(null);
    const timestampList = useRef(null);
    
    const perspective = useRef(93);

    const [streaming, setStreaming] = useState(false);
    const [currentStream, setCurrentStream] = useState(null);
    const [streamVisible, setStreamVisible] = useState(null);
        
    const streamVideoId = useRef('AfGvYLf99fg');
    
    const nextPageToken = useRef(null);

    const genreFilter = useRef('Movie');
    
    const categoryType = useRef([
        'Comedy',
        'Romance',
        'Drama',
        'Adventure',
        'Recommended',
        'Documentary',
        'Biography',
        'Comedy',
        'Romance',
        'Drama',
        'Adventure',
        'Documentary',
        'Comedy',
        'Romance',
        'Drama',
        'Adventure',
    ]);

    const categoryData = useRef(null);
    const streamData = useRef(null);
    
    const videoPlayerStreamWorker = useRef(null);

    const maximumCategoryStreamReferences = useRef(200);
    const streamReferences = useRef([]);
    const categoryDisplayReferences = useRef([]);
    const streamCurrentReference = useRef(null);
    
    const iframeReference = useRef(null);
    const iframeSrcReference = useRef(null);
    
    const frameAnchorReference = useRef(null);
    const frameSizeReference = useRef(null);
    const frameResolutionReference = useRef(null);
    const frameWrapperReference = useRef(null);
    const frameBaseReference = useRef(null);
    const frameContainerReference = useRef(null);
    const iframeBaseReference = useRef(null);
    
    let columnIndex = useRef(1);
    const rowIndexes = useRef([]);
    
    const initializedStart = useRef(false);

    const initialized = useRef(false);
    const initializedStream = useRef(false);
    const initializedSlots = useRef(false);

    const event = useRef(null);

    const queuedEvents = useRef({
        event: []
    });
    
    const timer = useRef(null);
    
    const queuedTimers = useRef({
        timer: []
    });
    
    const timers = useRef([
        { name: 'Frame Timer', duration: 56, value: 0 },
        { name: 'Destination Timer', duration: 71, value: 0 },
        { name: 'Movement Timer', duration: 127, value: 0 }
    ]);
    
    const moduleTypes = useRef([]);
    const moduleIndex = useRef(2);
    const [modulesInitialized, setModulesInitialized] = useState(false);

    const [tuning, setTuning] = useState(false);
    const [tuningStateValues, setTuningStateValues] = useState(null);
    const tuningStatus = useRef(false);
    
    const queuedMoveCategories = useRef(false);
    
    const tuningValues = useRef({
        columnIndex: 0,
        rowIndex: 0,
        position: {x: 0, y: 0},
        rotation: {x: 0, y: 0, z: 0},
        skew: {x: 0, y: 0},
        width: 0,
        height: 0,
        perspective: 0,
    });
    
    const serializedProps = useRef(null);
    
    const [videoSearchActiveStatus, setVideoSearchActiveStatus] = useState(false);
    
    useLayoutEffect(() => {
        
        const interval = setInterval(() => {
            timers.current.forEach(timer => {
                timer.value += 10;
                if(timer.value >= timer.duration){
                    timer.value = 0;
                    if(timer.name !== "Movement Timer"){
                        queuedTimers.current.timer.push(timer.name);
                    }
                    if(timer.name === "Movement Timer"){
                        queuedTimers.current.timer.unshift(timer.name);
                    }
                }
            });
            processShift();
        }, 10);

        if(initializedStart.current === doublePass){
            
            /* UNCOMMENT TO UPDATE SERVER */
            //updateAllVideoData();

            if(tuningStatus.current === true){
                initializeTuning();
            }

            console.log("Initializing Video Player");
            
            videoPlayerStreamWorker.current = new WebWorker(videoPlayerWorker);
            
            videoPlayerStreamWorker.current.onmessage = (event) => {
                queuedEvents.current.event.push(event);
            };
            
            window.addEventListener('popstate', closeStream);
            
            initializeModuleLinks();
            
            initialized.current = true;
        }
                
        return () => {
            clearInterval(interval);
            if(initializedStart.current === doublePass){
                videoPlayerStreamWorker.current.terminate();
                window.removeEventListener('popstate', closeStream);
            }
            initializedStart.current = true;
        };
    }, []);
    
    const requestAnimationFrame = (callback) => {
        const currentTime = Date.now();
        const timeToCall = Math.max(0, 16 - (currentTime - lastTime));
        const id = setTimeout(() => callback(currentTime + timeToCall), timeToCall);
        lastTime = currentTime + timeToCall;
        return id;
    };
    
    const chooseProfile = async (profileId) => {
        try {
            const response = await axios.get('https://ipapi.co/json/');
            userId.current = profileId + response.data.ip;
            console.log(userId.current);
            setChoosingProfile(false);
        } catch (error) {
            console.error(error);
        }
        
    }
 
    
    let lastTime = Date.now();
    
    const touchCategoryIndex = useRef(0);
    const touchDirection = useRef(null);
    
    const handleVideoTouchStart = (category, row, event) => {
        touchCategoryIndex.current = category;
        SwipeDetector.handleSwipeStart(event);
    };

    const handleVideoTouchEnd = async (event) => {
        touchDirection.current = await SwipeDetector.handleSwipeEnd(event);
        switch (touchDirection.current) {
            case 'right':
                descendRow(touchCategoryIndex.current - columnIndex.current + 1);
                break;
            case 'left':
                ascendRow(touchCategoryIndex.current - columnIndex.current + 1);
                break;
            case 'up':
                descendColumn();
                break;
            case 'down':
                ascendColumn();
                break;
            // Handle other directions if needed
            default:
                break;
        }
    };
    
    const initializeTuning = () => {
        tuningValues.current = {
            columnIndex: 0,
            rowIndex: 0,
            position: {x: 0, y: 0},
            rotation: {x: 0, y: 0, z: 0},
            skew: {x: 0, y: 0},
            width: 0,
            height: 0,
            perspective: 0,
        }
        setTuningStateValues(tuningValues.current);
        setTuning(true);
    }
    
    useLayoutEffect(() => {
        
        if(artificialIntelligenceActiveStatusReference.current !== artificialIntelligenceActiveStatus){
            artificialIntelligenceActiveStatusReference.current = artificialIntelligenceActiveStatus;
        }
        
        if(videoPlayerActiveStatusReference.current !== videoPlayerActiveStatus){
            videoPlayerActiveStatusReference.current = videoPlayerActiveStatus;
            timestampFrame.current = new Date().getTime();
        }
        
        if(videoPlayerActiveStatusReference.current == 0){
            transitionSpeed.current = 1;
        }
        if(videoPlayerActiveStatusReference.current == 1){
            transitionSpeed.current = 1;
        }
        if(videoPlayerActiveStatusReference.current == 2){
            transitionSpeed.current = 20;
        }
        
        if(videoPlayerActiveStatusReference.current !== 3){
            setCurrentStream(null);
        }
        
        mobileActiveStatusReference.current = mobileActiveStatus;
        
    }, [videoPlayerActiveStatus, mobileActiveStatus, artificialIntelligenceActiveStatus]);
    
    
    const initializeModuleLinks = () => {
        moduleTypes.current = [];
        moduleTypes.current.push({
            id: "Vanilla", 
            standalone: {
                position: {x: 35, y: -1}
            },
            mobile: {
                position: {x: 35, y: -5}
            }
        })
        moduleTypes.current.push({
            id: "Linear", 
            standalone: {
                position: {x: 57, y: -1}
            },
            mobile: {
                position: {x: 57, y: -5}
            }
        })
        moduleTypes.current.push({
            id: "Arc", 
            standalone: {
                position: {x: 35, y: 91}
            },
            mobile: {
                position: {x: 35, y: 95}
            }
        })
        moduleTypes.current.push({
            id: "Vortex", 
            standalone: {
                position: {x: 57, y: 91}
            },
            mobile: {
                position: {x: 57, y: 95}
            }
        })
        setModulesInitialized(true);
        initializeCategories();
    }
    
    const module = useRef(null);
    
    const initializeSlots = async (id) => {
        closeStream();
        initializedSlots.current = false;
        setStreaming(false);
        setCurrentStream(null);
        initializedStream.current = false;
        module.current = await VideoPlayerModuleLoader(id); 
        updateWorkerStreamIndices();
        queuedMoveCategories.current = true;
        setStreaming(true);
        initializedStream.current = true;
        initializedSlots.current = true;
    }
    
    const updateAllVideoData = () => {
        
        axios.get(`${apiUrl}/api/updateAllVideoData`)
            .then((response) => {
                if (response.data.success) {
                    console.log('Videos fetched and stored successfully.');
                    console.log(response.data.filteredStreams);
                } else {
                    console.error('Failed to fetch and store videos.');
                }
            })
            .catch((error) => console.error('Error fetching and storing videos:', error));
    };
    
    const processShift = () => { 
        startTime.current = new Date().getTime();
                
        if(queuedEvents.current.event.length > 0){
            processEvent();
        }
        
        if(queuedTimers.current.timer.length > 0){
            processTimer();
        }
        
        if(debug.current){
            endTime.current = new Date().getTime() - startTime.current;
            console.log("Main Clock: " + endTime.current);
        }
    }
    
    const processEvent = () =>{  
        event.current = queuedEvents.current.event.shift();
        
        if(event.current.data.type == "Stream Data") {
            streamData.current = null;
            streamData.current = event.current.data.payload;
        }
    }
    
    const processTimer = () => {
        timer.current = queuedTimers.current.timer.shift();
        
        if(timer.current === "Frame Timer" && videoPlayerActiveStatusReference.current !== 3){
            moveFrame();
        };
        
        if(timer.current === "Destination Timer" && initializedStream.current && initializedSlots.current && (videoPlayerActiveStatusReference.current === 2 || videoPlayerActiveStatusReference.current === 3)){
            moveListDestinations();
        };
        
        if(timer.current === "Movement Timer" && initializedStream.current && (videoPlayerActiveStatusReference.current === 2 || videoPlayerActiveStatusReference.current === 3)){
            moveListPositions();
        };
    }
    
    const updateWorkerStreamData = () => {
        serializedProps.current = {
            streamData: streamData.current,
        }
        videoPlayerStreamWorker.current.postMessage({type: "Update Stream Data", payload: serializedProps.current });
    }
    
    const updateWorkerStreamIndices = () => {
        serializedProps.current = {
            debug: debug.current,
            module: module.current,
            categoryType: categoryType.current,
            columnIndex: columnIndex.current,
            rowIndexes: rowIndexes.current,
            videoPlayerActiveStatusReference: videoPlayerActiveStatusReference.current,
            mobileActiveStatusReference: mobileActiveStatusReference.current,
        };
        videoPlayerStreamWorker.current.postMessage({type: "Update Stream Indices", payload: serializedProps.current });
    }
    
    const getVideoById = (videoId) => {
        
        return new Promise((resolve, reject) => {
            axios.get(`${apiUrl}/api/search/video/${videoId}`)
                .then(async (response) => {
                    console.log(videoId);
                    console.log(response.data);
                    resolve(response.data);
                })
                .catch((error) => {
                    console.error('Error fetching videos:', error);
                    reject(error);
                });
            });
        
    }

    const genrePlaylist = useRef(null);
    
    const categoryLoadedCount = useRef(0);
    
    const initializeCategories = async () => {
        console.log("Category Data:");
        rowIndexes.current = [];
        categoryData.current = [];
        categoryLoadedCount.current = 0;
        for(let category = 0; category < categoryType.current.length; category++){ 
            rowIndexes.current.push(0);
            getCategoryType(categoryType.current[category], category);
            
        }
    };
    
    const getCategoryType = async (type, typeIndex) => {
        if(type === 'Recommended'){
            categoryData.current[typeIndex] = await RecommendGet(userId.current, 'genre', 11);
            categoryLoadedCount.current = categoryLoadedCount.current + 1;
        }
        if(type !== 'Recommended'){
            genrePlaylist.current = await GetVideosByGenre(type); 
            if(genrePlaylist.current){
                categoryData.current[typeIndex] = genrePlaylist.current;
                categoryLoadedCount.current = categoryLoadedCount.current + 1;
            }
            else{
                getCategoryType(type, typeIndex);
            }
        }
        if(categoryLoadedCount.current === categoryType.current.length - 1){
            dataCheck();
        }
    }
    
    const dataCheck = () => {
        if(categoryData.current.includes(undefined)){
            console.log("Category undefined");
            requestAnimationFrame(dataCheck);
        }
        else{
            initializeStream();
        }
    }
    
    const streamCategorySlots = useRef(null);
    
    const failedFulfillment = useRef(false);
    
    const initializeStream = async () => {
        try{
            failedFulfillment.current = false;
            streamData.current = [];
            for(let category = 0; category < categoryType.current.length; category++){ 
                streamData.current.push({rowData: []});
                streamCategorySlots.current = categoryData.current[category];
                console.log("Attemping To Load Category Into Stream");
                if(streamCategorySlots.current){
                    for(let row = 0; row < streamCategorySlots.current.length; row++){
                        streamData.current[category].rowData.push({
                            position: {x: 0, y: 0}, 
                            absolutePosition: {x: 0, y: 0}, 
                            destinationPosition: {x: 0, y: 0}, 
                            rotation: {x: 0, y: 0, z: 0}, 
                            absoluteRotation: {x: 0, y: 0, z: 0}, 
                            destinationRotation: {x: 0, y: 0, z: 0}, 
                            skew: {x: 0, y: 0},
                            absoluteSkew: {x: 0, y: 0},
                            destinationSkew: {x: 0, y: 0},
                            width: 0, 
                            absoluteWidth: 0,
                            destinationWidth: 0, 
                            height: 0, 
                            absoluteHeight: 0,
                            destinationHeight: 0,
                            opacity: 0,
                            absoluteOpacity: 0,
                            destinationOpacity: 0,
                            perspective: 0,
                            absolutePerspective: 0,
                            destinationPerspective: 0,
                            interactive: 'auto',
                            data: streamCategorySlots.current[row]
                        });
                    }
                    console.log("Loaded Category Into Stream");
                }
                else{
                    failedFulfillment.current = true;
                    category = categoryType.current.length;
                    console.log("Fulfillment Failed");
                }
                
            }
            if(failedFulfillment.current === false){
                streamVisibility(false)
                updateWorkerStreamData();
                initializeSlots(moduleTypes.current[moduleIndex.current].id);
                setVideoPlayerActiveStatus(0);
                console.log("Fullfillment Success");
            }
            else{
                console.log("Failed: Reinitializing Categories");
                initializeCategories(); 
            }
        } catch (error) {
            console.log(error);
            initializeCategories();
        }
    };
    
    
    
    const openStream = async (category, index, direct) => {
        streamVisibility(false);
        if(direct){
            try{
                const loadedStream = await getVideoById(index);
                setCurrentStream(loadedStream);
                streamVideoId.current = loadedStream.videoId;
                openVideoID(index);
                //setSubMenuActiveStatus(false);
                setVideoSearchActiveStatus(false);
                console.log(loadedStream);
            } catch (error) {
                console.log("Requerying");
                //openStream('0', index, 'true');
            }
        }
        if(!direct){
            setCurrentStream(streamData.current[category].rowData[index].data);
            streamVideoId.current = streamData.current[category].rowData[index].data.videoId;
            openVideoID(streamData.current[category].rowData[index].data.videoId);
        }
        setSubMenuActiveStatus(true);
        pauseVideoPlayer();
    }
    
    // AI ENGINE REMOVE NEXT
    openStreamGlobal.current = openStream;
    
    const startStream = async () => {
        
        try{
            const loadedStream = await getVideoById(streamVideoId.current);
            RecommendSet(loadedStream, userId.current);
        } catch (error) {
            console.log(error);
        }
        
        playVideoPlayer();
        streamVisibility(true);
    }
    
    const closeStream = () => {
        pauseVideoPlayer();
        streamVisibility(false);
        if(iframeReference.current !== null){
            iframeSrcReference.current = iframeReference.current.src;
            iframeReference.current.src = iframeSrcReference.current;
        }
        setCurrentStream(null);
        setSubMenuActiveStatus(false);
    }
    
    const streamVisibility = (visibility) => {
        if(visibility){
            frameReference.current.style.zIndex = 8200;
            frameReference.current.style.pointerEvents = 'auto';
        }
        if(!visibility){
            frameReference.current.style.zIndex = -1;
            frameReference.current.style.pointerEvents = 'none';
        }
        setStreamVisible(visibility);
    }

    const ascendColumn = () => {
        if(columnIndex.current > -module.current.slots.length){
            columnIndex.current = columnIndex.current - 1;
        }
        if(columnIndex.current <= -module.current.slots.length){
            columnIndex.current = streamData.current.length - module.current.slots.length;
        }
        moveCategories();
        closeStream();
        updateWorkerStreamIndices();
    }
    
    const descendColumn = () => {
        if(columnIndex.current < streamData.current.length){
            columnIndex.current = columnIndex.current + 1;
        }
        if(columnIndex.current >= streamData.current.length){
            columnIndex.current = 0;
        }
        moveCategories();
        closeStream();
        updateWorkerStreamIndices();
    }
    
    const slotDeviationAscendRow = useRef(null);
    
    const ascendRow = (sliderIndex) => {
        slotDeviationAscendRow.current = 0;
        
        if(columnIndex.current < 0 && sliderIndex < Math.abs(columnIndex.current) + 1){
            slotDeviationAscendRow.current = (streamData.current.length);
        }
        if(columnIndex.current + sliderIndex > streamData.current.length && sliderIndex > Math.abs(columnIndex.current - streamData.current.length) - 1){
            slotDeviationAscendRow.current = -(streamData.current.length);
        }
        
        if(rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current] < streamData.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current].rowData.length){
            rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current] = rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current] + 1;
        }
        if(rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current] >= streamData.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current].rowData.length){
            rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationAscendRow.current] = 0;
        }
        closeStream();
        updateWorkerStreamIndices();
    }
    
    const slotDeviationDescendRow = useRef(null);
    
    const descendRow = (sliderIndex) => {
        slotDeviationDescendRow.current = 0;
        
        if(columnIndex.current < 0 && sliderIndex < Math.abs(columnIndex.current) + 1){
            slotDeviationDescendRow.current = (streamData.current.length);
        }
        if(columnIndex.current > streamData.current.length && sliderIndex > Math.abs(columnIndex.current - streamData.current.length) - 1){
            slotDeviationDescendRow.current = -(streamData.current.length);
        }
        
        if(rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current] > -module.current.slots[0].length){
            rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current] = rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current] - 1;
        }
        if(rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current] <= -module.current.slots[0].length){
            rowIndexes.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current] = streamData.current[sliderIndex + columnIndex.current - 1 + slotDeviationDescendRow.current].rowData.length - module.current.slots[0].length;
        }
        closeStream();
        updateWorkerStreamIndices();
    }
    
    const moveCategories = () => {
        for(let categoryIndex = 0; categoryIndex < module.current.categoryDisplays.length; categoryIndex++){
            
            if(categoryIndex + columnIndex.current < 0){
                categoryDisplayReferences.current[categoryIndex].textContent = categoryType.current[categoryType.current.length - Math.abs(categoryIndex + columnIndex.current) - 1];
            }
            if(categoryIndex + columnIndex.current === 0){
                categoryDisplayReferences.current[categoryIndex].textContent = categoryType.current[categoryType.current.length - 1];
            }
            if(categoryIndex + columnIndex.current > categoryType.current.length){
                categoryDisplayReferences.current[categoryIndex].textContent = categoryType.current[categoryIndex + columnIndex.current - categoryType.current.length - 1];
            }
            if(categoryIndex + columnIndex.current > 0 && categoryIndex + columnIndex.current <= categoryType.current.length){
                categoryDisplayReferences.current[categoryIndex].textContent = categoryType.current[columnIndex.current + categoryIndex - 1];
            }
        }
        setCategoriesOpacity(0);
    }
    
    const setCategoriesOpacity = (opacityValue) => {
        for(let displayIndex = 0; displayIndex < categoryDisplayReferences.current.length; displayIndex++){
            if(categoryDisplayReferences.current[displayIndex]){
                categoryDisplayReferences.current[displayIndex].opacity = opacityValue;
            }
        }
    }
    
    const moveListDestinations = () => {
        if(streamData.current !== null && initializedStream.current){
            if(queuedMoveCategories.current && categoryDisplayReferences.current.length > 0){
                queuedMoveCategories.current = false;
                moveCategories();
            }
        }   
    }
    
    const moveListPositions = () => {
        if(streamData.current !== null){
            
            for(let column = 0; column < categoryType.current.length; column++){
                for(let row = 0; row < streamData.current[column].rowData.length; row++){
                    streamCurrentReference.current = streamReferences.current[column * maximumCategoryStreamReferences.current + row];
                    
                    streamCurrentReference.current.style.top = `${streamData.current[column].rowData[row].position.y}%`;
                    streamCurrentReference.current.style.left = `${streamData.current[column].rowData[row].position.x}%`;
                    streamCurrentReference.current.style.width = `${streamData.current[column].rowData[row].width}%`;
                    streamCurrentReference.current.style.height = `${streamData.current[column].rowData[row].height}%`;
                    streamCurrentReference.current.style.opacity = `${streamData.current[column].rowData[row].opacity}`;
                    streamCurrentReference.current.style.transform = "skew(" + streamData.current[column].rowData[row].skew.x + "deg, " + streamData.current[column].rowData[row].skew.y + "deg) perspective(" + streamData.current[column].rowData[row].perspective + "px) rotateX(" + streamData.current[column].rowData[row].rotation.x + "deg) rotateY(" + streamData.current[column].rowData[row].rotation.y + "deg) rotateZ(" + streamData.current[column].rowData[row].rotation.z + "deg)";
                    streamCurrentReference.current.style.pointerEvents = `${streamData.current[column].rowData[row].interactive}`;
                    
                    if(tuningStatus.current === true && tuningValues.current.columnIndex === column && tuningValues.current.rowIndex === row){
                        streamCurrentReference.current.style.top = `${tuningValues.current.position.y}%`;
                        streamCurrentReference.current.style.left = `${tuningValues.current.position.x}%`;
                        streamCurrentReference.current.style.width = `${tuningValues.current.width}%`;
                        streamCurrentReference.current.style.height = `${tuningValues.current.height}%`;
                        streamCurrentReference.current.style.transform = "skew(" + tuningValues.current.skew.x + "deg, " + tuningValues.current.skew.y + "deg) perspective(" + tuningValues.current.perspective + "px) rotateX(" + tuningValues.current.rotation.x + "deg) rotateY(" + tuningValues.current.rotation.y + "deg) rotateZ(" + tuningValues.current.rotation.z + "deg)";
                        streamCurrentReference.current.style.pointerEvents = `${tuningValues.current.interactive}`;
                    }
                }
            }
        }
    }
    
    const deltaTimeFrame = useRef(null);
    const progressFrame = useRef(null);
    const interpolatedScaffoldScaleValue = useRef(null);
    const interpolatedFrameScaleValue = useRef(null);
    
    const moveFrame = () => {
        if (timestampFrame.current === null){
            timestampFrame.current = new Date().getTime();
        }

        if(videoPlayerActiveStatusReference.current === 0 || videoPlayerActiveStatusReference.current === 1 || videoPlayerActiveStatusReference.current === 2){
            glowImageSize.current = videoGlowScales.current[videoPlayerActiveStatusReference.current];
            videoPlayerScaffoldCurrentScale.current = videoPlayerScaffoldScales.current[videoPlayerActiveStatusReference.current];
            videoPlayerFrameCurrentScale.current = videoPlayerFrameScales.current[videoPlayerActiveStatusReference.current];

            deltaTimeFrame.current = new Date().getTime() - timestampFrame.current;
            progressFrame.current = Math.min(deltaTimeFrame.current / lerpSpeedDurationFrame.current, 1);

            interpolatedScaffoldScaleValue.current = lerp(0, videoPlayerScaffoldCurrentScale.current, progressFrame.current);
            interpolatedFrameScaleValue.current = lerp(0, videoPlayerFrameCurrentScale.current, progressFrame.current);

            scaffoldImageSize.current = interpolatedScaffoldScaleValue.current;
            frameImageSize.current = interpolatedFrameScaleValue.current;

            glowReference.current.style.transition = `transform ${transitionSpeed.current}s linear`
            glowReference.current.style.transform = `matrix(${glowImageSize.current}, 0, 0, ${glowImageSize.current} , 0, 0)`;
            scaffoldReference.current.style.transform = `matrix(${scaffoldImageSize.current}, 0, 0, ${scaffoldImageSize.current} , 0, 0)`;
            frameReference.current.style.transform = `matrix(${frameImageSize.current}, 0, 0, ${frameImageSize.current} , 0, 0)`;

            if(progressFrame.current > .98 && videoPlayerActiveStatusReference.current == 2){
                updateWorkerStreamIndices();
                setVideoPlayerActiveStatus(3);
                console.log("Set Video Active: " + 3);
            }
        }        
    };
    
    const videoPlaying = useRef(false);
    const videoPlayerToggled = useRef(false);
    
    const toggleVideoPlay = () => {
        console.log("Toggling: " + videoPlaying.current);
        videoPlayerToggled.current = false;
        if (iframeReference.current) {
            if(videoPlaying.current === false){
                playVideoPlayer();
                videoPlayerToggled.current = true;
            }
            if(videoPlaying.current === true && videoPlayerToggled.current === false){
                pauseVideoPlayer();
            }
        }
    };
    
    const videoPlayImageReference = useRef(null);
    const videoPauseImageReference = useRef(null);
    
    const pauseVideoPlayer = () => {
        if(iframeReference.current) {
            if(iframeReference.current.pauseVideo) {
                console.log("Pausing");
                iframeReference.current.pauseVideo();
                videoPlayerOverlayReference.current.style.opacity = 1;
                videoPlayImageReference.current.style.opacity = 1;
                videoPauseImageReference.current.style.opacity = 0;
                videoPlaying.current = false; 
                overlayStartTime.current = null;
            }
        }
    };
    
    const playVideoPlayer = () => {
        if(iframeReference.current) {
            if(iframeReference.current.playVideo) {
                console.log("Playing");
                iframeReference.current.playVideo();
                videoPlayImageReference.current.style.opacity = 0;
                videoPauseImageReference.current.style.opacity = 1;
                videoPlaying.current = true; 
                overlayStartTime.current = null;
                initiatedPlaying.current = false;
            }
        }
    };
    
    const videoFullScreen = useRef(false);

    const toggleFullScreen = () => {
        console.log("Fullscreen: " + videoFullScreen.current);
        if(iframeBaseReference.current){
            videoFullScreen.current = !videoFullScreen.current;
            if(videoFullScreen.current === false){
                console.log("Turning off fullscreen");
                if(document.fullscreenElement) {
                    document.exitFullscreen();
                } else if (document.mozFullScreenElement){
                    document.mozCancelFullScreen();
                } else if (document.msFullscreenElement){
                    document.msExitFullscreen();
                } else if (document.webkitFullscreenElement){
                    document.webkitExitFullscreen();
                }
            }
            if(videoFullScreen.current === true){
                if(iframeBaseReference.current.requestFullscreen){
                    iframeBaseReference.current.requestFullscreen();
                } else if (iframeBaseReference.current.mozRequestFullScreen){
                    iframeBaseReference.current.mozRequestFullScreen();
                } else if (iframeBaseReference.current.msRequestFullscreen){
                    iframeBaseReference.current.msRequestFullscreen();
                } else if (iframeBaseReference.current.webkitRequestFullscreen){
                    iframeBaseReference.current.webkitRequestFullscreen();
                } else if (iframeBaseReference.current.webkitEnterFullscreen){
                    iframeBaseReference.current.webkitEnterFullscreen();
                }
            }
        }
    }
    
    const videoPlayerOverlayReference = useRef(null);
    const videoPlayerPlaying = useRef(false);

    const initiatedPlaying = useRef(false);
    
    const handlePlayerState = (playing) => {
        console.log("Receiving State: " + playing);
        if(videoPlayerOverlayReference.current){
            if(playing && videoPlaying.current === true){
                if(initiatedPlaying.current === false){
                    initiatedPlaying.current = true;
                    hideOverlay();
                }                
            } else {
                overlayStartTime.current = null;
                initiatedPlaying.current = false;
                videoPlayerOverlayReference.current.style.opacity = 1;
            }
        }
    }
    
    const playTime = (time) => {
        iframeReference.current.seekTo(time);
        console.log(time);
    }
    
    const getPlayTime = () => {
        if(iframeReference.current.getCurrentTime){
            return iframeReference.current.getCurrentTime();
        }
    }
    
    const getDuration = () => {
        if(iframeReference.current.getDuration){
            return iframeReference.current.getDuration();
        }
    }
    
    const overlayStartTime = useRef(null);
    const overlayCurrentTime = useRef(null);
    const overlayDuration = useRef(3500);
    
    const hideOverlay = () => {
        if(overlayStartTime.current === null){
            overlayStartTime.current = new Date().getTime();
        }
        overlayCurrentTime.current = new Date().getTime();
        if(overlayCurrentTime.current - overlayStartTime.current >= overlayDuration.current){
            overlayStartTime.current = null;
            initiatedPlaying.current = false;
            videoPlayerOverlayReference.current.style.opacity = 0;
        }
        else{
            requestAnimationFrame(hideOverlay);
        }
    }
    
    const openVideoID = (videoId) => {
        if(iframeReference.current){
            if(iframeReference.current.loadVideoById){
                iframeBaseReference.current.style.opacity = 1;
                iframeReference.current.loadVideoById(videoId);
                console.log("Video ID: " + videoId);
            }
        }
    }

    const lerp = (start, end, t) => {
        return start * (1 - t) + end * t;
    }

    const tuningAltered = useRef(false);
    const temporaryColumnIndex = useRef(0);
    const temporaryRowIndex = useRef(0);
    const tuningRowSlots = useRef([]);
    const alteredRowSlots = useRef([]);
    
    const handleTuningChange = (event, property) => {
        const { value } = event.target;
        tuningAltered.current = false;
        if(property === "columnIndex"){
            temporaryColumnIndex.current = parseInt(value);
            temporaryRowIndex.current = tuningValues.current.rowIndex;
            tuningRowSlots.current = module.current.slots[temporaryColumnIndex.current];
            if(temporaryColumnIndex.current >= 0 && temporaryColumnIndex.current < module.current.columnSlotCount && temporaryRowIndex.current >= 0 && temporaryRowIndex.current < module.current.rowSlotCount){
                tuningValues.current = {
                    columnIndex: temporaryColumnIndex.current,
                    rowIndex: temporaryRowIndex.current,
                    position: {x: tuningRowSlots.current[temporaryRowIndex.current].x, y: tuningRowSlots.current[temporaryRowIndex.current].y},
                    rotation: {x: tuningRowSlots.current[temporaryRowIndex.current].rx, y: tuningRowSlots.current[temporaryRowIndex.current].ry, z: tuningRowSlots.current[temporaryRowIndex.current].rz},
                    skew: {x: tuningRowSlots.current[temporaryRowIndex.current].sx, y: tuningRowSlots.current[temporaryRowIndex.current].sy},
                    width: tuningRowSlots.current[temporaryRowIndex.current].w,
                    height: tuningRowSlots.current[temporaryRowIndex.current].h,
                    perspective: tuningRowSlots.current[temporaryRowIndex.current].p,
                };
                setTuningStateValues(tuningValues.current);
            }
        }
        if(property === "rowIndex"){
            temporaryColumnIndex.current = tuningValues.current.columnIndex;
            temporaryRowIndex.current = parseInt(value);
            tuningRowSlots.current = module.current.slots[temporaryColumnIndex.current];
            if(temporaryColumnIndex.current >= 0 && temporaryColumnIndex.current < module.current.columnSlotCount && temporaryRowIndex.current >= 0 && temporaryRowIndex.current < module.current.rowSlotCount){
                tuningValues.current = {
                    columnIndex: temporaryColumnIndex.current,
                    rowIndex: temporaryRowIndex.current,
                    position: {x: tuningRowSlots.current[temporaryRowIndex.current].x, y: tuningRowSlots.current[temporaryRowIndex.current].y},
                    rotation: {x: tuningRowSlots.current[temporaryRowIndex.current].rx, y: tuningRowSlots.current[temporaryRowIndex.current].ry, z: tuningRowSlots.current[temporaryRowIndex.current].rz},
                    skew: {x: tuningRowSlots.current[temporaryRowIndex.current].sx, y: tuningRowSlots.current[temporaryRowIndex.current].sy},
                    width: tuningRowSlots.current[temporaryRowIndex.current].w,
                    height: tuningRowSlots.current[temporaryRowIndex.current].h,
                    perspective: tuningRowSlots.current[temporaryRowIndex.current].p,
                };
                setTuningStateValues(tuningValues.current);
            }
        }
        if(property !== "columnIndex" && property !== "rowIndex"){
            setTuningStateValues(prevState => ({
                ...prevState,
                [property]: value
            }));
        }
        
        if(property === "positionX"){
            tuningValues.current.position.x = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "positionY"){
            tuningValues.current.position.y = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "rotationX"){
            tuningValues.current.rotation.x = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "rotationY"){
            tuningValues.current.rotation.y = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "rotationZ"){
            tuningValues.current.rotation.z = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "skewX"){
            tuningValues.current.skew.x = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "skewY"){
            tuningValues.current.skew.y = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "width"){
            tuningValues.current.width = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "height"){
            tuningValues.current.height = parseInt(value);
            tuningAltered.current = true;
        }
        if(property === "perspective"){
            tuningValues.current.perspective = parseInt(value);
            tuningAltered.current = true;
        }
        if(tuningAltered.current){
            alteredRowSlots.current = module.current.slots[tuningValues.current.columnIndex];
            alteredRowSlots.current[tuningValues.current.rowIndex].x = tuningValues.current.position.x;
            alteredRowSlots.current[tuningValues.current.rowIndex].y = tuningValues.current.position.y;
            alteredRowSlots.current[tuningValues.current.rowIndex].rx = tuningValues.current.rotation.x;
            alteredRowSlots.current[tuningValues.current.rowIndex].ry = tuningValues.current.rotation.y;
            alteredRowSlots.current[tuningValues.current.rowIndex].rz = tuningValues.current.rotation.z;
            alteredRowSlots.current[tuningValues.current.rowIndex].sx = tuningValues.current.skew.x;
            alteredRowSlots.current[tuningValues.current.rowIndex].sy = tuningValues.current.skew.y;
            alteredRowSlots.current[tuningValues.current.rowIndex].w = tuningValues.current.width;
            alteredRowSlots.current[tuningValues.current.rowIndex].h = tuningValues.current.height;
            alteredRowSlots.current[tuningValues.current.rowIndex].p = tuningValues.current.perspective;
        }
    }
    
    const artificialIntelligenceButtonImageReference = useRef(null);
    
    const artificialIntelligenceActiveStatusReference = useRef(false);
             
    const toggleArtificialIntelligence = () => {
        if(artificialIntelligenceActiveStatusReference.current === false){
            setArtificialIntelligenceActiveStatus(true);
            artificialIntelligenceButtonImageReference.current.style.filter = 'hue-rotate(90deg)';
        }
        if(artificialIntelligenceActiveStatusReference.current === true){
            setArtificialIntelligenceActiveStatus(false);
            artificialIntelligenceButtonImageReference.current.style.filter = 'hue-rotate(0deg)';
        }
    }
                    
    
    return (
        <>
            <div ref={glowReference} className="video-player-glow-container"
                style={{
                    transform: `matrix(${glowImageSize.current}, 0, 0, ${glowImageSize.current} , 0, 0)`,
                    transition: `transform ${transitionSpeed}s linear`,
                    top: mobileActiveStatus ? '54vh' : '48.5vh'
                }}
            >
                <div className="video-player-glow">
                    <div className="video-player-glow-image"></div>
                </div>
            </div>

            <div ref={scaffoldReference} 
                className="video-player-scaffold"
                style={{
                    transform: `matrix(${scaffoldImageSize.current}, 0, 0, ${scaffoldImageSize.current} , 0, 0)`,
                    top: mobileActiveStatus ? '34vh' : '28.5vh'
                }}
            >
                <div className="video-player-base">
                    <div className="video-player-display-container"
                        style={{
                            top: mobileActiveStatus ? '7.5%' : orientationActiveStatus ? '35%' : '20%',
                            left: mobileActiveStatus ? '5%' : orientationActiveStatus ? '5%' : '15%',
                            width: mobileActiveStatus ? '90%' : orientationActiveStatus ? '90%' : '70%',
                            height: mobileActiveStatus ? '90%' : orientationActiveStatus ? '30%' : '60%'
                        }}
                    >
                        <div className="video-player-display">
                            <div className="video-player-display-image"></div>
                        </div>
                        { choosingProfile && (
                            <Profiles chooseProfile={chooseProfile} ref={iframeReference} />
                        )}
                        <div className="video-player-list">

                            { streaming && (
                                <>
                                    {tuning && (
                                        <div className="video-player-tuner">
                                            <p style={{top: '-1%', left: '0%'}} className="video-player-tuner-type">Column Index</p>
                                            <input style={{top: '5%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.columnIndex} onChange={(event) => handleTuningChange(event, 'columnIndex')} />
                                            <p style={{top: '5%', left: '0%'}} className="video-player-tuner-type">Row Index</p>
                                            <input style={{top: '11%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.rowIndex} onChange={(event) => handleTuningChange(event, 'rowIndex')} />
                                            <p style={{top: '11%', left: '0%'}} className="video-player-tuner-type">Position X</p>
                                            <input style={{top: '17%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.position.x} onChange={(event) => handleTuningChange(event, 'positionX')} />
                                            <p style={{top: '19%', left: '0%'}} className="video-player-tuner-type">Position Y</p>
                                            <input style={{top: '23%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.position.y} onChange={(event) => handleTuningChange(event, 'positionY')} />
                                            <p style={{top: '23%', left: '0%'}} className="video-player-tuner-type">Rotation X</p>
                                            <input style={{top: '29%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.rotation.x} onChange={(event) => handleTuningChange(event, 'rotationX')} />
                                            <p style={{top: '29%', left: '0%'}} className="video-player-tuner-type">Rotation Y</p>
                                            <input style={{top: '35%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.rotation.y} onChange={(event) => handleTuningChange(event, 'rotationY')} />
                                            <p style={{top: '35%', left: '0%'}} className="video-player-tuner-type">Rotation Z</p>
                                            <input style={{top: '41%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.rotation.z} onChange={(event) => handleTuningChange(event, 'rotationZ')} />
                                            <p style={{top: '41%', left: '0%'}} className="video-player-tuner-type">Skew X</p>
                                            <input style={{top: '47%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.skew.x} onChange={(event) => handleTuningChange(event, 'skewX')} />
                                            <p style={{top: '47%', left: '0%'}} className="video-player-tuner-type">Skew Y</p>
                                            <input style={{top: '53%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.skew.y} onChange={(event) => handleTuningChange(event, 'skewY')} />
                                            <p style={{top: '53%', left: '0%'}} className="video-player-tuner-type">Width</p>
                                            <input style={{top: '59%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.width} onChange={(event) => handleTuningChange(event, 'width')} />
                                            <p style={{top: '59%', left: '0%'}} className="video-player-tuner-type">Height</p>
                                            <input style={{top: '65%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.height} onChange={(event) => handleTuningChange(event, 'height')} />
                                            <p style={{top: '66%', left: '0%'}} className="video-player-tuner-type">Perspective</p>
                                            <input style={{top: '71%', left: '0%'}} className="video-player-tuner-dial" type="number" value={tuningStateValues.perspective} onChange={(event) => handleTuningChange(event, 'perspective')} />
                                        </div>
                                    )}
                                    {modulesInitialized && (
                                        <>
                                            {moduleTypes.current.map((link, linkIndex) => (
                                                <div key={linkIndex}
                                                    className="video-player-module-link" 
                                                    style={{ 
                                                        top: mobileActiveStatus ? `${link.standalone.position.y}%` : `${link.mobile.position.y}%`, 
                                                        left: mobileActiveStatus ? `${link.standalone.position.x}%` : `${link.mobile.position.x}%`, 
                                                        fontSize: mobileActiveStatus ? '36px' : '12px',
                                                    }}
                                                    onClick={() => initializeSlots(link.id)}
                                                    >
                                                    {link.id}
                                                </div>
                                            ))}
                                        </>
                                    )}
                                    <div className="video-player-information-screen"
                                        style={{ 
                                            top: mobileActiveStatus ? `${module.current.informationScreen.standalone.y}%` : `${module.current.informationScreen.mobile.y}%`, 
                                            left: mobileActiveStatus ? `${module.current.informationScreen.standalone.x}%` : `${module.current.informationScreen.mobile.x}%`,
                                            width: mobileActiveStatus ? `${module.current.informationScreen.standalone.w}%` : `${module.current.informationScreen.mobile.w}%`, 
                                            height: mobileActiveStatus ? `${module.current.informationScreen.standalone.h}%` : `${module.current.informationScreen.mobile.h}%`,
                                            transform: mobileActiveStatus ? 
                                                `rotateX(${module.current.informationScreen.standalone.rx}deg) rotateY(${module.current.informationScreen.standalone.ry}deg) rotateZ(${module.current.informationScreen.standalone.rz}deg) translateZ('5000px')` 
                                                : 
                                                `rotateX(${module.current.informationScreen.mobile.rx}deg) rotateY(${module.current.informationScreen.mobile.ry}deg) rotateZ(${module.current.informationScreen.mobile.rz}deg) translateZ('5000px')`,
                                            WebkitTransform: 'translateZ(5000px)'
                                        }}
                                        >
                                        {currentStream && (
                                            <div className="video-player-information-screen-data">
                                                {!mobileActiveStatus && (
                                                    <div className="video-player-information-screen-backdrop"></div>
                                                )}
                                                <div className="video-player-close-button"
                                                    style={{
                                                        left: mobileActiveStatus ? '90%' : '92.5%'
                                                    }}
                                                    onClick={() => closeStream()}
                                                >
                                                    <div className="video-player-close-button-image"></div>
                                                </div>
                                                {!streamVisible && (
                                                    <>
                                                        <div className="video-player-start-stream-button" onClick={() => startStream()}>Watch Now</div>
                                                        <div className="video-player-information-screen-title" 
                                                            style={{
                                                                fontSize: mobileActiveStatus ? 
                                                                    `${module.current.titleSize.standalone}px` 
                                                                    : 
                                                                    `${module.current.titleSize.mobile}px`
                                                            }}
                                                        >
                                                        {currentStream.title}
                                                        </div>
                                                        <div className="video-player-information-screen-rating">{currentStream.rating}</div>
                                                        <div className="video-player-information-screen-release">{currentStream.release}</div>
                                                        <div className="video-player-information-screen-imdb">{currentStream.imdb}</div>
                                                        <div className="video-player-information-screen-genre">{currentStream.genre}</div>
                                                        <div className="video-player-information-screen-director">{currentStream.director}</div>
                                                        <div className="video-player-information-screen-cast">{currentStream.cast}</div>
                                                        <div className="video-player-information-screen-description">{currentStream.description}</div>
                                                    </>
                                                )}
                                                <img className="video-player-information-screen-thumbnail" src={currentStream.thumbnail.medium.url} alt={currentStream.title} />
                                            </div>
                                        )}
                                    </div>
                                    {streamData.current.map((category, categoryIndex) => (
                                        <div key={categoryIndex} className={"video-player-list-category"}>    
                                            {category.rowData.map((stream, streamIndex) => (
                                                <div key={streamIndex}
                                                    ref={(ref) => (streamReferences.current[categoryIndex * maximumCategoryStreamReferences.current + streamIndex] = ref)}
                                                    className="video-player-list-item" 
                                                    style={{ 
                                                        top: `${stream.position.y}%`, 
                                                        left: `${stream.position.x}%`, 
                                                        width: `${stream.width}%`, 
                                                        height: `${stream.height}%`, 
                                                        transform: `rotateX(${stream.rotation.x}deg) rotateY(${stream.rotation.y}deg) rotateZ(${stream.rotation.z}deg)`
                                                    }}
                                                    onClick={() => openStream(categoryIndex, streamIndex, false)}
                                                    onTouchStart={(event) => handleVideoTouchStart(categoryIndex, streamIndex, event)}
                                                    onTouchEnd={handleVideoTouchEnd}
                                                    >
                                                    <img className="video-player-list-item-image" src={stream.data.thumbnail.medium.url} alt={stream.data.title} />
                                                </div>
                                            ))}
                                        </div>
                                    ))} 
                                    {module.current.verticalSliders.map((slider, sliderIndex) => (
                                        <div key={sliderIndex}
                                            className="video-player-list-slider" 
                                            style={{ 
                                                top: '0px', 
                                                left: '0px', 
                                            }}
                                            >
                                            <div className="video-player-list-slider-button" 
                                                style={{ 
                                                    top: `${slider.descend.position.y}%`, 
                                                    left: `${slider.descend.position.x}%`,
                                                    transform: `rotateX(${slider.descend.rotation.x}deg) rotateY(${slider.descend.rotation.y}deg) rotateZ(${slider.descend.rotation.z}deg)`,
                                                }}
                                                onClick={() => descendColumn()}
                                                >
                                                <div className="video-player-list-slider-image"></div>
                                            </div>
                                            <div className="video-player-list-slider-button" 
                                                style={{ 
                                                    top: `${slider.ascend.position.y}%`, 
                                                    left: `${slider.ascend.position.x}%`,
                                                    transform: `rotateX(${slider.ascend.rotation.x}deg) rotateY(${slider.ascend.rotation.y}deg) rotateZ(${slider.ascend.rotation.z}deg)`,
                                                }}
                                                onClick={() => ascendColumn()}
                                                >
                                                <div className="video-player-list-slider-image"></div>
                                            </div>
                                        </div>
                                    ))}
                                    {module.current.horizontalSliders.map((slider, sliderIndex) => (
                                        <div key={sliderIndex}
                                            className="video-player-list-slider" 
                                            style={{ 
                                                top: '0px', 
                                                left: '0px', 
                                            }}
                                            >
                                            <div className="video-player-list-slider-button" 
                                                style={{ 
                                                    top: `${slider.descend.position.y}%`, 
                                                    left: `${slider.descend.position.x}%`, 
                                                    transform: `rotateX(${slider.descend.rotation.x}deg) rotateY(${slider.descend.rotation.y}deg) rotateZ(${slider.descend.rotation.z}deg)`,
                                                }}
                                                onClick={() => descendRow(sliderIndex)}
                                                >
                                                <div className="video-player-list-slider-image"></div>
                                            </div>
                                            <div className="video-player-list-slider-button" 
                                                style={{ 
                                                    top: `${slider.ascend.position.y}%`, 
                                                    left: `${slider.ascend.position.x}%`,
                                                    transform: `rotateX(${slider.ascend.rotation.x}deg) rotateY(${slider.ascend.rotation.y}deg) rotateZ(${slider.ascend.rotation.z}deg)`,
                                                }}
                                                onClick={() => ascendRow(sliderIndex)}
                                                >
                                                <div className="video-player-list-slider-image"></div>
                                            </div>
                                        </div>
                                    ))}
                                    {module.current.categoryDisplays.map((category, categoryIndex) => (
                                        <div key={categoryIndex}
                                            ref={(ref) => (categoryDisplayReferences.current[categoryIndex] = ref)}
                                            className="video-player-list-category-display" 
                                            style={{ 
                                                top: `${category.standalone.position.y}%`, 
                                                left: `${category.standalone.position.x}%`,
                                                width: `${category.standalone.width}%`,
                                                height: `${category.standalone.height}%`,
                                                fontSize: mobileActiveStatus ? `${category.standalone.fontSize}vh` : `${category.mobile.fontSize}vh`,
                                                transform: `rotateX(${category.standalone.rotation.x}deg) rotateY(${category.standalone.rotation.y}deg) rotateZ(${category.standalone.rotation.z}deg)`,
                                            }}
                                            >
                                        </div>

                                    ))}
                                    {videoSearchActiveStatus ? ( 
                                            <SearchBar setSearching={setVideoSearchActiveStatus} openStream={openStream} ref={iframeReference} />
                                        ) : (
                                            <>
                                                {!currentStream && (
                                                    <>
                                                        <div className="video-player-search-button" onClick={() => { setVideoSearchActiveStatus(true); setSubMenuActiveStatus(true)}}>
                                                            <div className="video-player-search-button-image"></div>
                                                        </div>
                                                        <div className="artificial-intelligence-toggle-button" onClick={() => {toggleArtificialIntelligence();}}>
                                                            <div ref={artificialIntelligenceButtonImageReference} className="artificial-intelligence-toggle-button-image"></div>
                                                        </div>
                                                    </>
                                                )}
                                            </>
                                        )
                                    }
                                </>
                            )}

                        </div>
                    </div>
                </div>
            </div>
        
            <div ref={frameReference}  className="video-player-frame"
                style={{
                    transform: `matrix(${frameImageSize.current}, 0, 0, ${frameImageSize.current} , 0, 0)`,
                    top: mobileActiveStatus ? '50vh' : '41vh'
                }}
            >
                <VideoIFramePlayer videoId={streamVideoId.current} playTime={playTime} getPlayTime={getPlayTime} getDuration={getDuration} toggleFullScreen={toggleFullScreen} onPlayChange={handlePlayerState} frameAnchorReference={frameAnchorReference} frameSizeReference={frameSizeReference} frameResolutionReference={frameResolutionReference} frameBaseReference={frameBaseReference} frameWrapperReference={frameWrapperReference} frameContainerReference={frameContainerReference} iframeBaseReference={iframeBaseReference} videoPlayerOverlayReference={videoPlayerOverlayReference} toggleVideoPlay={toggleVideoPlay} videoPlayImageReference={videoPlayImageReference} videoPauseImageReference={videoPauseImageReference} openStream={openStream} userId={userId.current} ref={iframeReference} />
                { streaming && (
                        <>
                            <div className="video-player-frame-anchor"
                                ref={frameAnchorReference}
                                style={{ 
                                    top: `${module.current.displayScreen.standalone.y}vh`, 
                                    left: mobileActiveStatus ? `${module.current.displayScreen.standalone.x}%` : `${module.current.displayScreen.mobile.x}%`,
                                }}
                                >
                                <div className="video-player-frame-size"
                                    ref={frameSizeReference}
                                    style={{ 
                                        width: mobileActiveStatus ? `${module.current.displayScreen.standalone.w}%` : `${module.current.displayScreen.mobile.w}%`, 
                                        height: `${module.current.displayScreen.standalone.h}%`,
                                        transform: `rotateX(${module.current.displayScreen.standalone.rx}deg) rotateY(${module.current.displayScreen.standalone.ry}deg) rotateZ(${module.current.displayScreen.standalone.rz}deg)`,
                                    }}
                                    >
                                    <div className="video-player-frame-resolution"
                                        ref={frameResolutionReference}
                                    >
                                        <div className="video-player-frame-base"
                                            ref={frameBaseReference}
                                        >
                                            <div className="video-player-frame-wrapper"
                                                ref={frameWrapperReference}
                                            >
                                                <div className="video-player-frame-iframe-container"
                                                    ref={frameContainerReference}
                                                >
                                                </div>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </>
                    )}
                            
            </div>
            
        </>
    );
};

export default VideoPlayer;