import React, { createContext, useState, useRef, useEffect, useContext } from 'react';
import MobileDetector from './components/MobileDetector';
import { doublePass } from './config.js'

const GlobalContext = createContext();

export const GlobalProvider = ({ children }) => {
    const [userId, setUserId] = useState('');
    const [videoPlayerActiveStatus, setVideoPlayerActiveStatus] = useState(-1);
    const [engineActiveStatus, setEngineActiveStatus] = useState(-1);
    const [chatRoomActiveStatus, setChatRoomActiveStatus] = useState(-1);
    const [moodActiveStatus, setMoodActiveStatus] = useState('Bad');
    const [subMenuActiveStatus, setSubMenuActiveStatus] = useState(false);
    const [mobileActiveStatus, setMobileActiveStatus] = useState(false);
    const [orientationActiveStatus, setOrientationActiveStatus] = useState(false);
    const [artificialIntelligenceActiveStatus, setArtificialIntelligenceActiveStatus] = useState(false);
    
    const openStreamGlobal = useRef(null);
    
    const initializedStart = useRef();
    
    const orientation = useRef();
    const orientationSecond = useRef();
    
    useEffect(() => {
        
        window.addEventListener("resize", checkOrientation);

        checkOrientation();
        
        return () => {
            window.removeEventListener("resize", checkOrientation);
        };
    }, []);
    
    const checkOrientation = () => {
        orientation.current = window.matchMedia("(orientation: portrait)").matches;
        orientationSecond.current = window.innerHeight > window.innerWidth;
        setOrientationActiveStatus(orientation.current);
        console.log(orientation.current);
        console.log(orientationSecond.current);
    }
    
    const handleCheckMobile = (value) => {
        setMobileActiveStatus(value);
    }
    
    return (
        <>
            <MobileDetector setMobile={handleCheckMobile} />
       
            <GlobalContext.Provider 
                value={{ 
                    userId,
                    setUserId,
                    mobileActiveStatus,
                    orientationActiveStatus,
                    videoPlayerActiveStatus,
                    setVideoPlayerActiveStatus, 
                    engineActiveStatus,
                    setEngineActiveStatus,
                    chatRoomActiveStatus,
                    setChatRoomActiveStatus,
                    moodActiveStatus,
                    setMoodActiveStatus,
                    subMenuActiveStatus,
                    setSubMenuActiveStatus,
                    artificialIntelligenceActiveStatus,
                    setArtificialIntelligenceActiveStatus,
                    openStreamGlobal
                }}
                >
                {children}
            </GlobalContext.Provider>
        </>
    );
};

export const useGlobalState = () => useContext(GlobalContext);