import React, { useRef, forwardRef, useEffect } from 'react';

const VideoPlayerSlider = forwardRef(({alterPlayTime, requestPlayTime, requestDuration}, ref) => {
    const sliderRef = useRef(null);
    const thumbRef = useRef(null);
    const isDraggingRef = useRef(false);

    const currentTime = useRef(0);
    const duration = useRef(0);

    const offsetX = useRef(0);
    
    const minPosDrag = useRef(0);
    const maxPosDrag = useRef(0);
    const percentageDrag = useRef(0);
    const newTimeDrag = useRef(0);
    
    const handleDragStart = (e) => {
        if(ref.current){
            minPosDrag.current = sliderRef.current.getBoundingClientRect().left;
            maxPosDrag.current = minPosDrag.current + sliderRef.current.offsetWidth;
            percentageDrag.current = (e.clientX - minPosDrag.current) / (maxPosDrag.current - minPosDrag.current);
            newTimeDrag.current = percentageDrag.current * duration.current;
            alterPlayTime(newTimeDrag.current);
        }
    };
    
    const minPosCurrent = useRef(0);
    const maxPosCurrent = useRef(0);
    const newPosCurrent = useRef(0);
    const percentageCurrent = useRef(0);
    const newTimeCurrent = useRef(0);

    useEffect(() => {
        const interval = setInterval(() => {
            if(ref.current){
                if (!isDraggingRef.current) {
                    currentTime.current = requestPlayTime();
                    duration.current = requestDuration();
                    minPosCurrent.current = 0;
                    maxPosCurrent.current = minPosCurrent.current + sliderRef.current.offsetWidth;
                    percentageCurrent.current = currentTime.current / duration.current;
                    newPosCurrent.current = minPosCurrent.current + (maxPosCurrent.current - minPosCurrent.current) * percentageCurrent.current;
                    thumbRef.current.style.left = `${newPosCurrent.current}px`;
                }
            }
        }, 300);
        
        return () => {
            clearInterval(interval);
        };
    }, []);

    return (
        <div ref={sliderRef} 
            className="video-player-frame-controls-slider"
            onMouseDown={handleDragStart}
        >
            <div
                ref={thumbRef}
                className="video-player-frame-controls-slider-thumb"
            />
        </div>
    );

})

export default VideoPlayerSlider;