export default () => {

    let base = self; // eslint-disable-line no-restricted-globals

    let startTime = 0;
    let endTime = 0;
    
    let windowData;
    let npcReferenceData;
    let npcPhysicsData;
    let npcAnimationData;
    let avatars;
    let directionX;
    let directionY;
    let posX;
    let posY;
    let npcFresnelPower;
    let npcLightAngle;
    let angleOffsetX;
    let angleOffsetY;
    
    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (eventData) => {
        if(eventData.type == "Update"){
            update(eventData.payload);
        }
    }

    /* Movement - Collision */
    const update = (eventData) => {
        startTime = new Date().getTime();
        
        windowData = eventData.windowData;
        npcReferenceData = eventData.npcReferenceData;
        npcPhysicsData = eventData.npcPhysicsData;
        npcAnimationData = eventData.npcAnimationData;
        avatars = eventData.engineData.levelData.avatars;
        
        
        /* Animation Angle */
        
        for(let npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){

            const directionX = npcPhysicsData.destinations[npcIndex].x - npcPhysicsData.absolutes[npcIndex].x;
            const directionY = npcPhysicsData.destinations[npcIndex].y - npcPhysicsData.absolutes[npcIndex].y;

            if(directionX < 30 && directionX > -30 && directionY > 30){
                npcAnimationData.animations[npcIndex].animationAngle = 0;
            }
            if(directionX > 30 && directionY > 30){
                npcAnimationData.animations[npcIndex].animationAngle = 1;
            }
            if(directionX > 30 && directionY < 30 && directionY > -30){
                npcAnimationData.animations[npcIndex].animationAngle = 2;
            }
            if(directionX > 30 && directionY < -30){
                npcAnimationData.animations[npcIndex].animationAngle = 3;
            }
            if(directionX < 30 && directionX > -30 && directionY < -30){
                npcAnimationData.animations[npcIndex].animationAngle = 4;
            }
            if(directionX < -30 && directionY < -30){
                npcAnimationData.animations[npcIndex].animationAngle = 5;
            }
            if(directionX < -30 && directionY < 30 && directionY > -30){
                npcAnimationData.animations[npcIndex].animationAngle = 6;
            }
            if(directionX < -30 && directionY > 30){
                npcAnimationData.animations[npcIndex].animationAngle = 7;
            }

            /* Animation Interaction */
 
            if(npcPhysicsData.interactions[npcIndex] == false && npcAnimationData.animations[npcIndex].animating == false){
                //console.log("SETTING TO Walk");
                npcAnimationData.animations[npcIndex].animationType = "walk";
            }   
            if(npcPhysicsData.interactions[npcIndex] == true && npcAnimationData.animations[npcIndex].animating == false){
                //console.log("SETTING TO STAND");
                npcAnimationData.animations[npcIndex].animationType = "stand";
            }


            // Mouth

            if(npcAnimationData.animations[npcIndex].talking && !npcAnimationData.animations[npcIndex].animating){
                npcAnimationData.animations[npcIndex].talkingCounter++;
                npcAnimationData.animations[npcIndex].mouth.color.state = npcAnimationData.animations[npcIndex].mouth.color.open;

                if(npcAnimationData.animations[npcIndex].animationAngle == 0){
                    npcAnimationData.animations[npcIndex].mouth.y = "38%";
                    npcAnimationData.animations[npcIndex].mouth.x = "48%";
                    npcAnimationData.animations[npcIndex].mouth.opacity = ".7";
                }
                if(npcAnimationData.animations[npcIndex].animationAngle == 2){
                    npcAnimationData.animations[npcIndex].mouth.y = "38%";
                    npcAnimationData.animations[npcIndex].mouth.x = "61%";
                    npcAnimationData.animations[npcIndex].mouth.opacity = ".7";
                }
                if(npcAnimationData.animations[npcIndex].animationAngle == 4){
                    npcAnimationData.animations[npcIndex].mouth.opacity = "0";
                }
                if(npcAnimationData.animations[npcIndex].animationAngle == 6){
                    npcAnimationData.animations[npcIndex].mouth.y = "38%";
                    npcAnimationData.animations[npcIndex].mouth.x = "39%";
                    npcAnimationData.animations[npcIndex].mouth.opacity = ".7";
                }

                if(npcAnimationData.animations[npcIndex].talkingCounter > npcAnimationData.animations[npcIndex].npcMouthDelaySpeed){
                    npcAnimationData.animations[npcIndex].talking = false;
                    npcAnimationData.animations[npcIndex].talkingCounter = 0;
                }
            }
            if(!npcAnimationData.animations[npcIndex].talking || npcAnimationData.animations[npcIndex].animating){
                npcAnimationData.animations[npcIndex].mouth.color.state = npcAnimationData.animations[npcIndex].mouth.color.shut;
                npcAnimationData.animations[npcIndex].mouth.opacity = "0";
            }

            
            /* Standing Animation */
            if(npcAnimationData.animations[npcIndex].animationType == "stand"){
                npcAnimationData.animations[npcIndex].animationFrame = 0;
                npcAnimationData.animations[npcIndex].animationAngle = 2;
                npcAnimationData.animations[npcIndex].movementLerp = 0;
            }
            /* Walking Animation */
            if(npcAnimationData.animations[npcIndex].animationType == "walk"){
                npcAnimationData.animations[npcIndex].animationFrame += 1;
                if(npcAnimationData.animations[npcIndex].animationFrame >= 7){
                    npcAnimationData.animations[npcIndex].animationFrame = 0;
                }
                npcAnimationData.animations[npcIndex].movementLerp = .1;
            }
            /* Running Animation */
            if(npcAnimationData.animations[npcIndex].animationType == "run"){
                npcAnimationData.animations[npcIndex].animationFrame += 1;
                if(npcAnimationData.animations[npcIndex].animationFrame >= 7){
                    npcAnimationData.animations[npcIndex].animationFrame = 0;
                }
                npcAnimationData.animations[npcIndex].movementLerp = .3;
                if(new Date().getTime() - npcAnimationData.animations[npcIndex].start > npcAnimationData.animations[npcIndex].duration){
                    //npcAnimationData.animations[npcIndex].animationType = "walk";
                    npcAnimationData.animations[npcIndex].animating = false;
                }
            }
            /* Jumping Animation */
            if(npcAnimationData.animations[npcIndex].animationType == "jump"){
                npcAnimationData.animations[npcIndex].animationFrame = 0;
                //npcAnimationData.animations[npcIndex].animationAngle = 0;
                npcAnimationData.animations[npcIndex].movementLerp = .8;
                if(npcAnimationData.animations[npcIndex].jumpHeight <= 0){
                    //npcAnimationData.animations[npcIndex].animationType = "walk";
                    npcAnimationData.animations[npcIndex].animating = false;
                }
            }
            /* Flipping Animation */
            if(npcAnimationData.animations[npcIndex].animationType == "flip"){
                npcAnimationData.animations[npcIndex].animationAngle = 2;
                npcAnimationData.animations[npcIndex].movementLerp = .8;
                npcAnimationData.animations[npcIndex].rotation += 45;
                if (npcAnimationData.animations[npcIndex].rotation >= 360) {
                    //npcAnimationData.animations[npcIndex].animationType = "walk";
                    npcAnimationData.animations[npcIndex].animating = false;
                    npcAnimationData.animations[npcIndex].rotation = 0;
                }
            }  
            
            /* Gravity Animation */
            if(npcAnimationData.animations[npcIndex].jumpHeight > 0){
                npcAnimationData.animations[npcIndex].jumpHeight -= 100;
                
            }
            if(npcAnimationData.animations[npcIndex].jumpHeight < 0){
                npcAnimationData.animations[npcIndex].jumpHeight = 0;
            }
            

            // Sprite

            posX = -111 * npcAnimationData.animations[npcIndex].animationAngle;
            posY = -150 * npcAnimationData.animations[npcIndex].animationFrame;

            npcAnimationData.animations[npcIndex].fresnel.image.x = posX;
            npcAnimationData.animations[npcIndex].fresnel.image.y = posY;
            npcAnimationData.animations[npcIndex].avatar.image.x = posX;
            npcAnimationData.animations[npcIndex].avatar.image.y = posY;

            // Fresnel

            npcFresnelPower = 3;

            npcLightAngle = Math.atan2((npcReferenceData.npcContainers[npcIndex].position.y - windowData.windowHalfHeight) - avatars[0].position.y, (npcReferenceData.npcContainers[npcIndex].position.x- windowData.windowHalfWidth) - avatars[0].position.x);

            angleOffsetX = - Math.cos(npcLightAngle) * npcFresnelPower;
            angleOffsetY = - Math.sin(npcLightAngle) * npcFresnelPower;

            npcAnimationData.animations[npcIndex].fresnel.x = angleOffsetX;
            npcAnimationData.animations[npcIndex].fresnel.y = angleOffsetY;
        }
        
        base.postMessage({type: "NPC Animation Data", payload: npcAnimationData});
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Animation Clock: " + endTime);
        }
    }    
}