import React, { Component, useLayoutEffect, useRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
import WebWorker from '../../WebWorker';
import DataPort from '../DataPort/index.js';
import AI from '../AI/index.js';
import priorityWorker from "../Worker/npcpriority.worker.js";
import physicsWorker from '../Worker/npcphysics.worker.js';
import movementWorker from '../Worker/npcmovement.worker.js';
import animationWorker from '../Worker/npcanimation.worker.js';
import { apiUrl, doublePass } from '../../config.js'

const NPCManager = ({props}) => {

    const endTime = useRef(0);
    
    // AI Engine remove next enable following
    const { videoPlayerActiveStatus, artificialIntelligenceActiveStatus } = useGlobalState();
    //const { engineActiveStatus } = useGlobalState();
    const engineActiveStatusReference = useRef();

    const windowData = useRef({
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
        windowHalfWidth: window.innerWidth/2,
        windowHalfHeight: window.innerHeight/2
    });
    
    const npcContainerReferences = useRef([]);
    const npcAvatarReferences = useRef([]);
    const npcFresnelReferences = useRef([]);
    const npcMouthReferences = useRef([]);
    const npcAIReferences = useRef([]);

    const npcData = useRef({
        npcReferenceData: {
            npcContainers: props.engineData.current.nonPlayerCharacterData.nonPlayerCharacters,
        },

        npcPriorityData: {
            objects: props.engineData.current.levelData.objects,
            avatars: [],
        },

        npcPhysicsData: {
            interactions: [],
            destinations: [],
            absolutes: [],
            positions: [],
            avatarOffsets: [],
        },

        npcAnimationData: {
            animations: [],
        },

        npcAIData: {
            concious: [],
        },
    });
    
    const npcPriorityWorker = useRef(null);
    const npcPhysicsWorker = useRef(null);
    const npcMovementWorker = useRef(null);
    const npcAnimationWorker = useRef(null);

    const timers = useRef([
        { name: 'Priority Timer', duration: 3000, value: 0 },
        { name: 'Physics Timer', duration: 101, value: 0 },
        { name: 'Movement Timer', duration: 91, value: 0 },
        { name: 'Animation Timer', duration: 73, value: 0 },
    ]);
    
    const processingQueued = useRef(false);
    
    const initializedStart = useRef(false);
    
    const initialized = useRef(false);
    const initializedPriority = useRef(false);
    const initializedPhysics = useRef(false);
        
    const updatedPhysics = useRef(false);
    
    const queuedEvents = useRef({
        event: []
    });
    
    let startTime = useRef(0);
    
    let lerpStartTime = useRef(new Date().getTime());
    
    const voice = useRef("");
    
    // AI ENGINE REMOVEN NEXT LINE
    const artificialIntelligenceActiveStatusReference = useRef(false);
    
    useLayoutEffect(() => {
        // AI Engine remove next two enable following
        engineActiveStatusReference.current = videoPlayerActiveStatus;
        artificialIntelligenceActiveStatusReference.current = artificialIntelligenceActiveStatus;
        //engineActiveStatusReference.current = engineActiveStatus;
    }, [videoPlayerActiveStatus, artificialIntelligenceActiveStatus]);
    //}, [engineActiveStatus]);
    
    useLayoutEffect(() => {
        
        const interval = setInterval(() => {
            timers.current.forEach(timer => {
                timer.value += 10;
                if(timer.value >= timer.duration){
                    timer.value = 0;
                    processTimer(timer.name);
                }
            });
        }, 10);

        if(initializedStart.current === doublePass){
            window.addEventListener("resize",function(){ 
                windowData.current.windowWidth = window.innerWidth;
                windowData.current.windowHeight = window.innerHeight;
                windowData.current.windowHalfWidth = window.innerWidth/2;
                windowData.current.windowHalfHeight = window.innerHeight/2;

                for(var index = 0; index < npcData.current.npcPhysicsData.avatarOffsets.length; index++){
                    npcData.current.npcPhysicsData.avatarOffsets[index] = {x: window.innerWidth/18, y: window.innerHeight/400};
                }
                synchronizeData();
            });

            npcPriorityWorker.current = new WebWorker(priorityWorker);
            npcPhysicsWorker.current = new WebWorker(physicsWorker);
            npcMovementWorker.current = new WebWorker(movementWorker);
            npcAnimationWorker.current = new WebWorker(animationWorker);

            npcPriorityWorker.current.onmessage = (event) => {
                queuedEvents.current.event.push(event);
            };

            npcPhysicsWorker.current.onmessage = (event) => {
                queuedEvents.current.event.push(event);
            };
            
            npcMovementWorker.current.onmessage = (event) => {
                queuedEvents.current.event.push(event);
            };

            npcAnimationWorker.current.onmessage = (event) => {
                queuedEvents.current.event.push(event);
            };

            initializeData();
            initialized.current = true;
        }
                        
        return () => {
            clearInterval(interval);
            
            if(initializedStart.current === doublePass){
                npcPriorityWorker.current.terminate();
                npcPhysicsWorker.current.terminate();
                npcMovementWorker.current.terminate();
                npcAnimationWorker.current.terminate();
            }
            initializedStart.current = true;
        };
    }, []); 
    
    // AI DELETE FOR ENGINE
    const npcRelativeStart = useRef([]);
    
    const npcContainer = useRef(null);
    
    const initializeData = () => {
        
        
        for(var index = 0; index < npcData.current.npcReferenceData.npcContainers.length; index++){
            props.engineData.current.levelData.avatars.push({ position: npcData.current.npcReferenceData.npcContainers[index].position, width: 15, height: 15, type: "Avatar", visible: false })
            
            npcData.current.npcPriorityData.avatars.push({indexes: []});
            
            npcContainer.current = npcData.current.npcReferenceData.npcContainers[index];
            npcData.current.npcPhysicsData.interactions.push(false);
            npcData.current.npcPhysicsData.destinations.push({x: npcContainer.current.position.x, y: npcContainer.current.position.y});
            npcData.current.npcPhysicsData.absolutes.push({x: npcContainer.current.position.x, y: npcContainer.current.position.y});
            npcData.current.npcPhysicsData.positions.push({x: npcContainer.current.position.x, y: npcContainer.current.position.y});
            npcData.current.npcPhysicsData.avatarOffsets.push({x: -window.innerHeight/18, y: -window.innerHeight/400});

            npcData.current.npcAnimationData.animations.push({
                animating: false,
                animationAngle: 0,
                animationFrame: 0,
                animationType: "walk",
                talking: false,
                talkingCounter: 0,
                npcMouthDelaySpeed: .1,
                mouth: npcContainer.current.mouth,
                avatar: npcContainer.current.avatar,
                fresnel: npcContainer.current.fresnel,
                rotation: 0,
                jumpHeight: 0,
                movementLerp: .01,
                lerpStartTime: new Date().getTime(),
                lerpProgress: 0.0001
            });
            
            npcData.current.npcAIData.concious.push({
                animation: null,
                duration: 0,
            });
            
            
            // AI DELETE FOR ENGINE
            npcRelativeStart.current[index] = {
                x: window.innerWidth - npcData.current.npcReferenceData.npcContainers[index].width/2,
                y: window.innerHeight - npcData.current.npcReferenceData.npcContainers[index].height/2,
            }
            
            npcData.current.npcReferenceData.npcContainers[index].position.x = npcRelativeStart.current[index].x;
            npcData.current.npcReferenceData.npcContainers[index].position.y = npcRelativeStart.current[index].y;
            npcData.current.npcPhysicsData.destinations[index].x = npcRelativeStart.current[index].x;
            npcData.current.npcPhysicsData.destinations[index].y = npcRelativeStart.current[index].y;
            npcData.current.npcPhysicsData.absolutes[index].x = npcRelativeStart.current[index].x;
            npcData.current.npcPhysicsData.absolutes[index].y = npcRelativeStart.current[index].y;
            npcData.current.npcPhysicsData.positions[index].x = npcRelativeStart.current[index].x;
            npcData.current.npcPhysicsData.positions[index].y = npcRelativeStart.current[index].y;

            for(var npcWaypointIndex = 0; npcWaypointIndex < npcData.current.npcReferenceData.npcContainers[index].waypoints.length; npcWaypointIndex++){
                if(npcWaypointIndex === 0){
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].x = npcRelativeStart.current[index].x;
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].y = npcRelativeStart.current[index].y;
                }
                if(npcWaypointIndex === 1){
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].x = npcData.current.npcReferenceData.npcContainers[index].width;
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].y = npcRelativeStart.current[index].y;
                }
                if(npcWaypointIndex === 2){
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].x = npcRelativeStart.current[index].x;
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].y = npcRelativeStart.current[index].y;
                }
                if(npcWaypointIndex === 3){
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].x = npcData.current.npcReferenceData.npcContainers[index].width;
                    npcData.current.npcReferenceData.npcContainers[index].waypoints[npcWaypointIndex].y = npcRelativeStart.current[index].y;
                }
                
            }
            
        }
        
        initializeDataStyle();
    }
    
    const initializeDataStyle = () => {   
        
        for(var npcIndex = 0; npcIndex < npcData.current.npcReferenceData.npcContainers.length; npcIndex++){
            npcContainer.current = npcData.current.npcReferenceData.npcContainers[npcIndex];
            
            npcContainerReferences.current[npcIndex].style.left = npcContainer.current.position.x + 'px';
            npcContainerReferences.current[npcIndex].style.top = npcContainer.current.position.y + 'px';
            npcContainerReferences.current[npcIndex].style.transform = 'rotate(' + "-" + npcContainer.current.rotation + 'deg)';
            
            npcAvatarReferences.current[npcIndex].style.backgroundPosition = npcContainer.current.avatar.image.x + 'px ' + npcContainer.current.avatar.image.y + 'px';
            npcAvatarReferences.current[npcIndex].style.left = npcContainer.current.avatar.x + 'px';
            npcAvatarReferences.current[npcIndex].style.top = npcContainer.current.avatar.y + 'px';
            
            npcFresnelReferences.current[npcIndex].style.backgroundPosition = npcContainer.current.fresnel.image.x + 'px ' + npcContainer.current.fresnel.image.y + 'px';
            npcFresnelReferences.current[npcIndex].style.left = npcContainer.current.fresnel.x + 'px';
            npcFresnelReferences.current[npcIndex].style.top = npcContainer.current.fresnel.y + 'px';
            
            npcMouthReferences.current[npcIndex].style.left = npcContainer.current.mouth.x + '%';
            npcMouthReferences.current[npcIndex].style.top = npcContainer.current.mouth.y + '%';
            npcMouthReferences.current[npcIndex].style.opacity = npcContainer.current.mouth.opacity;
            npcMouthReferences.current[npcIndex].style.backgroundColor = npcContainer.current.mouth.color.state;
        }
        
    }
    
    const serializedProps = useRef(null);
    
    //AI ENGINE REMOVE NEXT TWO LINES
    const initializedMovement = useRef(false);
    const npcContainerTEMPORARY = useRef(null);
    
    const processTimer = (timerName) => { 
        startTime.current = new Date().getTime();
        serializedProps.current = {
            debug: props.engineData.current.debug,
            mapWidth: props.engineData.current.mapWidth,
            mapHeight: props.engineData.current.mapHeight,
            mapWidthQuadrantCount: props.engineData.current.mapWidthQuadrantCount,
            mapHeightQuadrantCount: props.engineData.current.mapHeightQuadrantCount,
            engineData: props.engineData.current,
            windowData: windowData.current,
            npcReferenceData: npcData.current.npcReferenceData,
            npcPriorityData: npcData.current.npcPriorityData,
            npcPhysicsData: npcData.current.npcPhysicsData,
            npcAnimationData: npcData.current.npcAnimationData,   
            //AI ENGINE REMOVE NEXT LINE
            staticInteraction: artificialIntelligenceActiveStatusReference.current
        };
        
        processingQueued.current = false;
        if(queuedEvents.current.event.length > 0){
            processingQueued.current = true;
            processQueued();
        }
        
        if(timerName === "Movement Timer" && initializedPhysics.current === true && engineActiveStatusReference.current === 3){
            synchronizeData();
            npcMovementWorker.current.postMessage({type: "Update", payload: serializedProps.current });
        };

        if(timerName === "Priority Timer" && processingQueued.current === false && initialized.current === true && engineActiveStatusReference.current === 3){
            processingQueued.current = true;            
            if(initializedPriority.current === true){
                npcPriorityWorker.current.postMessage({type: "Update", payload: serializedProps.current });
            }
            if(initializedPriority.current === false){
                initializedPriority.current = true;
                npcPriorityWorker.current.postMessage({type: "Initialize", payload: serializedProps.current });
            }
        };

        if(timerName === "Physics Timer" && processingQueued.current === false && initialized.current === true && initializedPriority.current === true && engineActiveStatusReference.current === 3){
            processingQueued.current = true;
            if(initializedPhysics.current === true){
                npcPhysicsWorker.current.postMessage({type: "Update", payload: serializedProps.current });
            }
            if(initializedPhysics.current === false){
                initializedPhysics.current = true;
                npcPhysicsWorker.current.postMessage({type: "Initialize", payload: serializedProps.current });
            }
        };

        if(timerName === "Animation Timer" && processingQueued.current === false && initialized.current === true && initializedPhysics.current === true && engineActiveStatusReference.current === 3){
            processingQueued.current = true;
            npcAnimationWorker.current.postMessage({type: "Update", payload: serializedProps.current });
        };

        
        
        if(props.engineData.current.debug){
            endTime.current = new Date().getTime() - startTime.current;
            console.log("Main Clock: " + endTime.current);
        }
    }
        
    const event = useRef(null);
    
    
    const processQueued = () =>{  
        event.current = queuedEvents.current.event.shift();
        
        if(event.current.data.type == "NPC Priority Data") {
            npcData.current.npcPriorityData = event.current.data.payload;
            //console.log("Processing Priority");
            //console.log(npcData.current.npcPriorityData);
        }
        if(event.current.data.type == "NPC Physics Data") {
            npcData.current.npcPhysicsData.destinations = event.current.data.payload.destinations;
            npcData.current.npcPhysicsData.interactions = event.current.data.payload.interactions;
            updatedPhysics.current = true;
            //console.log("Processing PHYSICS");
            //console.log(npcData.current.npcPhysicsData);
        }
        if(event.current.data.type == "NPC Movement Data") {
            npcData.current.npcReferenceData.npcContainers = event.current.data.payload.containers; 
            npcData.current.npcPhysicsData.absolutes = event.current.data.payload.absolutes; 
            npcData.current.npcPhysicsData.positions = event.current.data.payload.positions; 
            //console.log("Processing MOVEMENT");
            //console.log(npcData.current.npcPhysicsData);
        }
        if(event.current.data.type == "NPC Animation Data") {
            npcData.current.npcAnimationData = event.current.data.payload; 
            //console.log("Processing ANIMATION");
            //console.log(npcData.current.npcAnimationData);
        }
    }

    const lerpTimestamp = useRef(0);
    const npcAnimation = useRef(null);
    const lerpCurrentTime = useRef(0);
    const lerpTotalTime = useRef(300);
    const lerpAdjustedTotalTime = useRef(0);
    
    /* Movement - Collision - Animation */
    const synchronizeData = () => {  
        
        //console.log(npcData.current.npcPriorityData.avatars);
        
        lerpTimestamp.current = new Date().getTime();
        
        if (updatedPhysics.current === true ) {
            for(var npcIndex = 0; npcIndex < npcData.current.npcReferenceData.npcContainers.length; npcIndex++){
                npcAnimation.current = npcData.current.npcAnimationData.animations[npcIndex];
                updatedPhysics.current = false;
                npcAnimation.current.lerpStartTime = lerpTimestamp.current;
                npcData.current.npcPhysicsData.absolutes[npcIndex].x = npcData.current.npcPhysicsData.positions[npcIndex].x;
                npcData.current.npcPhysicsData.absolutes[npcIndex].y = npcData.current.npcPhysicsData.positions[npcIndex].y;
                npcAnimation.current.lerpProgress = 0.0001;
            }
        }
        
        //console.log(props.engineData.current.levelData.avatars[0].position);
        //console.log(props.engineData.current.levelData.avatars[1].position);
        //console.log(props.engineData.current.levelData.avatars[2].position);
        //console.log(props.engineData.current.levelData.avatars[3].position);
        //console.log(props.engineData.current.levelData.avatars[4].position);
  
        for(var npcIndex = 0; npcIndex < npcData.current.npcReferenceData.npcContainers.length; npcIndex++){
            npcContainer.current = npcData.current.npcReferenceData.npcContainers[npcIndex];
            npcAnimation.current = npcData.current.npcAnimationData.animations[npcIndex];
            
            props.engineData.current.levelData.avatars[npcIndex + 1].position = npcContainer.current.position;
                                                           
            lerpCurrentTime.current = lerpTimestamp.current - npcAnimation.current.lerpStartTime;

            lerpAdjustedTotalTime.current = lerpTotalTime.current/npcAnimation.current.movementLerp;
            
            npcAnimation.current.lerpProgress = lerpCurrentTime.current / lerpAdjustedTotalTime.current;
            //console.log(npcAnimation.current);

            if (lerpCurrentTime.current >= lerpAdjustedTotalTime.current) {
                npcAnimation.current.lerpStartTime = lerpTimestamp.current;
                npcData.current.npcPhysicsData.absolutes[npcIndex].x = npcData.current.npcPhysicsData.positions[npcIndex].x;
                npcData.current.npcPhysicsData.absolutes[npcIndex].y = npcData.current.npcPhysicsData.positions[npcIndex].y;
                npcAnimation.current.lerpProgress = 0.0001;
            }

            //console.log(npcData.current);
            //console.log("DESTINATION X: " + npcData.current.npcPhysicsData.destinations[npcIndex].x);
            //console.log("ABSOLUTES X: " + npcData.current.npcPhysicsData.absolutes[npcIndex].x);
            //console.log("POSITION X: " + npcData.current.npcPhysicsData.positions[npcIndex].x);
            
            if(npcData.current.npcAIData.concious[npcIndex].animation !== null){
                npcAnimation.current.animationType = npcData.current.npcAIData.concious[npcIndex].animation;
                npcAnimation.current.animating = true;
                npcAnimation.current.duration = npcData.current.npcAIData.concious[npcIndex].duration;
                npcAnimation.current.start = new Date().getTime();
                
                if(npcData.current.npcAIData.concious[npcIndex].animation == "jump" || npcData.current.npcAIData.concious[npcIndex].animation == "flip"){
                    npcAnimation.current.jumpHeight = npcData.current.npcReferenceData.npcContainers[npcIndex].jump;
                }
                npcData.current.npcAIData.concious[npcIndex].animation = null;
            }
            
            npcContainer.current.rotation = npcAnimation.current.rotation;
            
        }
        synchronizeDataStyle();
    }
    
    const synchronizeDataStyle = () => { 
        
        for(var npcIndex = 0; npcIndex < npcData.current.npcReferenceData.npcContainers.length; npcIndex++){
            npcContainer.current = npcData.current.npcReferenceData.npcContainers[npcIndex];
            
            npcContainerReferences.current[npcIndex].style.left = (npcContainer.current.position.x - npcContainer.current.width/2) + 'px';
            npcContainerReferences.current[npcIndex].style.top = (npcContainer.current.position.y - npcContainer.current.height/2) + 'px';
            npcContainerReferences.current[npcIndex].style.transform = 'rotate(' + "-" + npcContainer.current.rotation + 'deg)';

            npcAvatarReferences.current[npcIndex].style.backgroundPosition = npcContainer.current.avatar.image.x + 'px ' + npcContainer.current.avatar.image.y + 'px';
            npcAvatarReferences.current[npcIndex].style.left = npcContainer.current.avatar.x + 'px';
            npcAvatarReferences.current[npcIndex].style.top = npcContainer.current.avatar.y + 'px';
            
            npcFresnelReferences.current[npcIndex].style.backgroundPosition = npcContainer.current.fresnel.image.x + 'px ' + npcContainer.current.fresnel.image.y + 'px';
            npcFresnelReferences.current[npcIndex].style.left = npcContainer.current.fresnel.x + 'px';
            npcFresnelReferences.current[npcIndex].style.top = npcContainer.current.fresnel.y + 'px';
            
            npcMouthReferences.current[npcIndex].style.left = npcContainer.current.mouth.x + '%';
            npcMouthReferences.current[npcIndex].style.top = npcContainer.current.mouth.y + '%';
            npcMouthReferences.current[npcIndex].style.opacity = npcContainer.current.mouth.opacity;
            npcMouthReferences.current[npcIndex].style.backgroundColor = npcContainer.current.mouth.color.state;
            
            
        }
        
    }
    
    const lerp = (start, end, t) => {
        return start * (1 - t) + end * t;
    }
    
    return (
        <>
            <div style={{top: '0px', left: '0px', width: '100%', height: '100%', position: 'relative'}}>
                {npcData.current.npcReferenceData.npcContainers.map((item, index) => (
                    <div
                        key={index}
                        ref={(ref) => (npcContainerReferences.current[index] = ref)}
                        className={'game-npc-container'}
                        style={{
                            left: `${item.position.x}px`,
                            top: `${item.position.y}px`,
                            width: `${item.width}px`,
                            height: `${item.height}px`,
                        }}>
                        <div style={{top: '0px', left: '0px', width: '100%', height: '100%', position: 'relative'}}>
                            <div 
                                ref={(ref) => (npcAvatarReferences.current[index] = ref)} 
                                className={'game-npc-avatar'} 
                                style={{ 
                                    backgroundImage: `url(/Bobtv/images/npc/${item.type}/avatar.png)`,
                                    top: `${item.avatar.x}px`,
                                    left: `${item.avatar.y}px`, 
                                    width: `${item.width}px`, 
                                    height: `${item.height}px` 
                                }}>
                                <div style={{top: '0px', left: '0px', width: '100%', height: '100%', position: 'absolute'}}>
                                    <div 
                                        ref={(ref) => (npcMouthReferences.current[index] = ref)} 
                                        className={'game-npc-mouth'} 
                                        style={{
                                            top: `${item.mouth.x}px`,
                                            left: `${item.mouth.y}px`, 
                                            width: `${item.mouth.width}px`, 
                                            height: `${item.mouth.height}px` 
                                        }}>
                                    </div>
                                </div>
                            </div>

                            <div 
                                ref={(ref) => (npcFresnelReferences.current[index] = ref)} 
                                className={'game-npc-fresnel'} 
                                style={{ 
                                    backgroundImage: `url(/Bobtv/images/npc/${item.type}/fresnel.png)`,
                                    top: `${item.fresnel.x}px`, 
                                    left: `${item.fresnel.y}px`, 
                                    width: `${item.width}px`, 
                                    height: `${item.height}px` 
                                }}>
                            </div>
                        </div>
                        <AI key={index} props={props} ref={npcData} npcIndex={index} />
                    </div>
                ))}
            </div>
        </>
    );
}

export default NPCManager;