import React, { useEffect, useLayoutEffect, useRef } from 'react';
import { useGlobalState  } from '../../globalcontext';
   

const Speech = ({ props }) => {

    const { artificialIntelligenceActiveStatus } = useGlobalState();
    
    const artificialIntelligenceActiveStatusReference = useRef(false);
    
    const recognition = useRef(null);
    const listening = useRef(false);
    
    const interimTranscript = useRef('');
    const finalTranscript = useRef('');
    const transcriptSegment = useRef('');
    
    useLayoutEffect(() => {
        if(artificialIntelligenceActiveStatusReference.current !== artificialIntelligenceActiveStatus){
            artificialIntelligenceActiveStatusReference.current = artificialIntelligenceActiveStatus;
            toggleRecognition();
        }
    }, [artificialIntelligenceActiveStatus]);

    const startRecognition = () => {
        if ('SpeechRecognition' in window || 'webkitSpeechRecognition' in window) {
            const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
            const recognitionInstance = new SpeechRecognition();
            recognitionInstance.lang = 'en-US'; // Set the language for recognition

            recognitionInstance.onstart = () => {
                console.log('Voice recognition started. Speak...');
            };

            recognitionInstance.onresult = (event) => {
                
                
                
                interimTranscript.current = '';
                finalTranscript.current = '';

                for (let i = event.resultIndex; i < event.results.length; i++) {
                    transcriptSegment.current = event.results[i][0].transcript;
                    if (event.results[i].isFinal) {
                        finalTranscript.current += transcriptSegment.current + ' ';
                    } else {
                        interimTranscript.currentt += transcriptSegment.current;
                    }
                }
                
                props.references.voiceReference.current = finalTranscript.current;
                console.log('Transcript:', finalTranscript.current);
            };

            recognitionInstance.onerror = (event) => {
                console.log('Speech recognition error:', event.error);
            };
            
            recognitionInstance.onend = () => {
                console.log("Listening stopped");
                if (listening.current) {
                    console.log("Restarting listening...");
                    recognitionInstance.start();
                }
            };

            recognitionInstance.start();
            recognitionInstance.continuous = true;
            recognition.current = recognitionInstance;
        } else {
            console.error('Speech recognition not supported in this browser.');
        }
    };

    const stopRecognition = () => {
        if (recognition.current) {
            listening.current = false;
            recognition.current.stop();
            console.log('Voice recognition stopped.');
        }
    };   

    const toggleRecognition = () => {
        if (!listening.current && artificialIntelligenceActiveStatusReference.current === true) {
            listening.current = true;
            startRecognition();
            console.log("Setting AI to true");
        }
        if(listening.current && artificialIntelligenceActiveStatusReference.current === false) {
            listening.current = false;
            stopRecognition();
        }
    };

    return (
        <div className="speech-container">
            <p style={{opacity: '0'}}>Transcript: {props.references.voiceReference.current}</p>
        </div>
    );
};

export default Speech;


