import React, { useState, useRef, useEffect } from 'react';
import { useGlobalState  } from '../../globalcontext';

const Background = () => {
    const { moodActiveStatus, engineActiveStatus } = useGlobalState();
    const backgroundGood = useRef(null);
    const backgroundBad = useRef(null);

    useEffect(() => {
        if(moodActiveStatus === 'Good'){
            backgroundGood.current.style.opacity = 1;
            backgroundBad.current.style.opacity = 0;
        }
        if(moodActiveStatus === 'Bad'){
            backgroundGood.current.style.opacity = 0;
            backgroundBad.current.style.opacity = 1;
        }
    }, [moodActiveStatus]);
    
    return (
        <>
            <div className="underlay-background-good" ref={backgroundGood}></div>
            <div className="underlay-background-bad" ref={backgroundBad}></div>
        </>
    );
};

export default Background;