export default () => {

    let base = self; // eslint-disable-line no-restricted-globals
    
    let startTime;
    let endTime;
    
    const distance = 80;

    const minimumClickDistance = 50;

    let initialized = false;
    
    let count = 0;
    
    let windowData;
    let gameReferenceData;
    let playerPriorityData;
    let playerPhysicsData;
    let mouseX;
    let mouseY;
    let centerX;
    let centerY;
    let mouseDistanceX;
    let mouseDistanceY;
    let angle;
    let mathCosAngle;
    let mathSinAngle;
    let destinationX;
    let destinationY;          
    let collision;
    let objectWidth;
    let objectHeight;
    let objectX;
    let objectY;
    let playerTrueOffsetX;
    let playerTrueOffsetY;
    let playerX;
    let playerY;
    let playerDestinationX;
    let playerDestinationY;
    let borderPadding;
    let distanceX;
    let distanceY;
    let halfDestinationX;
    let halfDestinationY; 

    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Update"){
            update(event.payload);
        }
    }
    
    /* Movement - Collision - Animation */
    const update = (eventData) => {
        startTime = new Date().getTime();
        if(eventData.mouseState.mouseDown || initialized == false){
            count = count + 1;
            initialized = true;
            
            windowData = eventData.windowData;
            gameReferenceData = eventData.gameReferenceData;
            playerPriorityData = eventData.playerPriorityData;
            playerPhysicsData = eventData.playerPhysicsData;
            
            mouseX = eventData.mouseState.mousePosition.x - gameReferenceData.gameContainerLeft;
            mouseY = eventData.mouseState.mousePosition.y - gameReferenceData.gameContainerTop;
            centerX = windowData.windowHalfWidth + playerPhysicsData.avatarOffsetX;
            centerY = windowData.windowHalfHeight + playerPhysicsData.avatarOffsetY;
            mouseDistanceX = centerX - mouseX;
            mouseDistanceY = centerY - mouseY;

            angle = Math.atan2(mouseY - centerY, mouseX - centerX);
            mathCosAngle = Math.cos(angle);
            mathSinAngle = Math.sin(angle);

            destinationX = playerPhysicsData.levelOffsetX - mathCosAngle * playerPhysicsData.playerMovementSpeed;
            destinationY = playerPhysicsData.levelOffsetY - mathSinAngle * playerPhysicsData.playerMovementSpeed;          

            collision = false;
            for(let x = 0; x < playerPriorityData.objectPriorityList.length; x++){ 
                if(!collision){
                    objectWidth = playerPriorityData.objectPriorityList[x].width/2;
                    objectHeight = playerPriorityData.objectPriorityList[x].height/2;

                    objectX = playerPriorityData.objectPriorityList[x].position.x + objectWidth;
                    objectY = playerPriorityData.objectPriorityList[x].position.y + objectHeight;

                    playerTrueOffsetX = playerPhysicsData.playerOffsetX + windowData.windowHalfWidth;
                    playerTrueOffsetY = playerPhysicsData.playerOffsetY + windowData.windowHalfHeight;

                    playerX = playerTrueOffsetX - mathCosAngle * -playerPhysicsData.playerMovementSpeed ;
                    playerY = playerTrueOffsetY - mathSinAngle * -playerPhysicsData.playerMovementSpeed;

                    playerDestinationX = playerX - playerPhysicsData.avatarOffsetX;
                    playerDestinationY = playerY - playerPhysicsData.avatarOffsetY;
                    
                    borderPadding = 13;

                    distanceX = objectX - playerX;
                    if(distanceX < objectWidth + borderPadding && distanceX > -objectWidth - borderPadding){
                        distanceY = objectY - playerY;
                        if(distanceY < objectHeight + borderPadding && distanceY > -objectHeight - borderPadding){
                            collision = true;
                        }
                    }
                }
            }
            if(!collision){
                if(destinationX < 730 && destinationY < 345 && destinationX > -5340 && destinationY > -5720){
                    if(mouseDistanceX > minimumClickDistance || mouseDistanceX < -minimumClickDistance || mouseDistanceY > minimumClickDistance || mouseDistanceY < -minimumClickDistance){
                        halfDestinationX = playerPhysicsData.levelOffsetX - mathCosAngle * (playerPhysicsData.playerMovementSpeed/2);
                        halfDestinationY = playerPhysicsData.levelOffsetY - mathSinAngle * (playerPhysicsData.playerMovementSpeed/2);  
                        playerPhysicsData.levelOffsetX = halfDestinationX;
                        playerPhysicsData.levelOffsetY = halfDestinationY;
                        playerPhysicsData.playerOffsetX = -halfDestinationX;
                        playerPhysicsData.playerOffsetY = -halfDestinationY;
                        base.postMessage({type: "Player Physics Data", payload: playerPhysicsData});
                    }
                }
            }
        }
        
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Physics Clock: " + endTime);
        }
    }
}