import React, { useEffect, useState, useRef } from 'react';
import { useGlobalState  } from '../globalcontext';

const About = () => {

    const { mobileActiveStatus, orientationActiveStatus } = useGlobalState();
    
    return (
        <div className="site-content">
            <div className="page-frame"
                style={{
                    top: mobileActiveStatus ? '32vh' : '26vh',
                }}
            >
                <div className="page-components">
                    
                </div>
                <div className="page-content">
                    <p>ABOUT</p>
                    <p style={{ paddingTop: '2vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>Robert Woodens Stream</p>
                    <p style={{ paddingTop: '2vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>Designed to show my skill level, not just a revolutionary UI but a serverless server backend. It contains a unique streaming service, that pushes the boundary of possibilities using html, css, react, nodejs, express, nginx, webworkers, babel, and many other forward facing languages and libraries.</p> 
                    <p style={{ paddingTop: '4vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>Designed to transform 2D environments into unique 3D perspective experiences, elegantly changing between dynamic module layouts, and seamless architecture.</p>
                    <p style={{ paddingTop: '4vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}>Using advanced mathmatics such as calculus and trigonometry and number theory elevated the current standard of 2D applications, such as a creating a algorithm that loops infinitely through dual omnidirectional dynamic matrixes.</p>
                    <p style={{ paddingTop: '8vh', fontSize: mobileActiveStatus ? '16px' : orientationActiveStatus ? '16px' : '9px'}}></p>
                </div>
            </div>
        </div>
    );
};

export default About;