import { Linear } from '../VideoPlayerModules/Linear/index.js';
import { Arc } from '../VideoPlayerModules/Arc/index.js';
import { Vortex } from '../VideoPlayerModules/Vortex/index.js';
import { Vanilla } from '../VideoPlayerModules/Vanilla/index.js';

const VideoPlayerModuleLoader = (name) => {
    
    let module;
    
    if(name == "Vanilla"){
        module = Vanilla;
    }
    if(name == "Linear"){
        module = Linear;
    }
    if(name == "Arc"){
        module = Arc;
    }
    if(name == "Vortex"){
        module = Vortex;
    }
    
    return module;
    console.log("Module Loaded")
}


export { VideoPlayerModuleLoader };
