export default () => {

    let base = self; // eslint-disable-line no-restricted-globals
    
    let startTime = 0;
    let endTime = 0;
    
    let maximumQuadrantPriorityDistance = 3000;
    let maximumPriorityDistance = 1500; 
    
    let objectQuadrants = [];
    
    self.onmessage = (event) => { // eslint-disable-line no-restricted-globals
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Initialize"){
            initialize(event.payload);
        }
        if(event.type == "Update"){
            update(event.payload);
        }
    }
    let objects;
    let objectQuadrantsWidth;
    let objectQuadrantsHeight;
    let quadrantIndex;
    let closestQuadrantDistance;
    let quadrantDistance;
    let quadrantDeltaX;
    let quadrantDeltaY;
    
    const initialize = (eventData) => {
        startTime = new Date().getTime();
        objects = eventData.engineData.levelData.objects;
        
        objectQuadrantsWidth = eventData.mapWidth / eventData.mapWidthQuadrantCount;
        objectQuadrantsHeight = eventData.mapHeight / eventData.mapHeightQuadrantCount;
        
        for(var w = 0; w < eventData.mapWidthQuadrantCount; w++){
            for(var h = 0; h < eventData.mapHeightQuadrantCount; h++){
                objectQuadrants.push({x: (objectQuadrantsHeight * h) + objectQuadrantsHeight/2, y: (objectQuadrantsWidth * w) + objectQuadrantsWidth/2, objectHolders: []});
            }
        }
        
        for(var o = 0; o < objects.length; o++){
            quadrantIndex = 0;
            closestQuadrantDistance = 10000000000;
            quadrantDistance = 0;
            for(var q = 0; q < objectQuadrants.length; q++){
                quadrantDeltaX = objectQuadrants[q].x - objects[o].position.x;
                quadrantDeltaY = objectQuadrants[q].y - objects[o].position.y;
                quadrantDistance = Math.sqrt(quadrantDeltaX * quadrantDeltaX + quadrantDeltaY * quadrantDeltaY);
                if(closestQuadrantDistance > quadrantDistance){
                    closestQuadrantDistance = quadrantDistance;
                    quadrantIndex = q;
                }
            }
            objectQuadrants[quadrantIndex].objectHolders.push(objects[o]);
        }
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Priority Clock: " + endTime);
        }
    }

    let playerPhysicsData;
    let playerPriorityData;
    let quadrantPriorityDeltaX;
    let quadrantPriorityDeltaY;
    let quadrantPriorityDistance;
    let distanceX;
    let distanceY;
    let distance;
                        
    const update = (eventData) => {
        startTime = new Date().getTime();
        objects = eventData.engineData.levelData.objects;
        playerPhysicsData = eventData.playerPhysicsData;
        playerPriorityData = {
            objectPriorityList: [],
        }
        
        for(let indexQuadrant = 0; indexQuadrant < objectQuadrants.length; indexQuadrant++){
            if(objectQuadrants[indexQuadrant].objectHolders.length > 0){
                quadrantPriorityDeltaX = objectQuadrants[indexQuadrant].x - playerPhysicsData.playerOffsetX;
                quadrantPriorityDeltaY = objectQuadrants[indexQuadrant].y - playerPhysicsData.playerOffsetY;
                quadrantPriorityDistance = Math.sqrt(quadrantPriorityDeltaX * quadrantPriorityDeltaX + quadrantPriorityDeltaY * quadrantPriorityDeltaY);
                if(quadrantPriorityDistance < maximumQuadrantPriorityDistance){
                    for(let indexObject = 0; indexObject < objectQuadrants[indexQuadrant].objectHolders.length; indexObject++){
                        distanceX = objectQuadrants[indexQuadrant].objectHolders[indexObject].position.x - playerPhysicsData.playerOffsetX;
                        distanceY = objectQuadrants[indexQuadrant].objectHolders[indexObject].position.y - playerPhysicsData.playerOffsetY;
                        distance = Math.sqrt(distanceX * distanceX + distanceY * distanceY);
                        if(distance < maximumPriorityDistance){
                            playerPriorityData.objectPriorityList.push(objectQuadrants[indexQuadrant].objectHolders[indexObject]);
                        }
                    }
                }
            }
        }

        base.postMessage({type: "Player Priority Data", payload: playerPriorityData });

        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Priority Clock: " + endTime);
        }
    }
}