import React, { useState, useRef, useLayoutEffect, Component } from 'react';
import { useGlobalState  } from '../../globalcontext';
import CrystalClouds from '../CrystalClouds';

const Widgets = () => {
    
    const { mobileActiveStatus } = useGlobalState();

    return (
        <div className="widgets"
            style={{
                top: mobileActiveStatus ? '32vh' : '26vh'
            }}
        >
            <CrystalClouds />
            
        </div>
    );
};

export default Widgets;