export default () => {

    let base = self; // eslint-disable-line no-restricted-globals
    
    let startTime;
    let endTime;
    
    const npcWaypointData = [];
    
    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Initialize"){
            initialize(event.payload);
        }
        if(event.type == "Update"){
            update(event.payload);
        }
    }
    
    let npcReferenceData;
    let npcPriorityData;
    let npcPhysicsData;
    let npcAnimationData;
    let npcContainer;
    let npcAnimation;      
    
    const initialize = (eventData) => {
        npcReferenceData = eventData.npcReferenceData;
        
        for(let npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){
            npcWaypointData.push({current: new Date().getTime(), previous: new Date().getTime(), waypointIndex: 0});
        }
    }
    
    /* Movement - Collision - Animation */
    const update = (eventData) => {
        startTime = new Date().getTime();
        npcReferenceData = eventData.npcReferenceData;
        npcPriorityData = eventData.npcPriorityData;
        npcPhysicsData = eventData.npcPhysicsData;
        npcAnimationData = eventData.npcAnimationData;

        for(var npcIndex = 0; npcIndex < npcReferenceData.npcContainers.length; npcIndex++){
            npcContainer = npcReferenceData.npcContainers[npcIndex];
            npcAnimation = npcAnimationData.animations[npcIndex];

            //console.log(npcData.current);
            //console.log("DESTINATION X: " + npcPhysicsData.destinations[npcIndex].x);
            //console.log("ABSOLUTES X: " + npcPhysicsData.absolutes[npcIndex].x);
            //console.log("POSITION X: " + npcPhysicsData.positions[npcIndex].x);

            npcPhysicsData.positions[npcIndex].x = lerp(npcPhysicsData.absolutes[npcIndex].x, npcPhysicsData.destinations[npcIndex].x, npcAnimation.lerpProgress);
            npcPhysicsData.positions[npcIndex].y = lerp(npcPhysicsData.absolutes[npcIndex].y, npcPhysicsData.destinations[npcIndex].y - npcAnimation.jumpHeight, npcAnimation.lerpProgress);

            //console.log("(" + npcPhysicsData.positions[npcIndex].x + ", " + npcPhysicsData.positions[npcIndex].y + ")");

            npcContainer.position.x = npcPhysicsData.positions[npcIndex].x;
            npcContainer.position.y = npcPhysicsData.positions[npcIndex].y;

            npcContainer.avatar.x = npcAnimation.avatar.x;
            npcContainer.avatar.y = npcAnimation.avatar.y;
            npcContainer.fresnel.x = npcAnimation.fresnel.x;
            npcContainer.fresnel.y = npcAnimation.fresnel.y;

            npcContainer.avatar.image.x = npcAnimation.avatar.image.x;
            npcContainer.avatar.image.y = npcAnimation.avatar.image.y;
            npcContainer.fresnel.image.x = npcAnimation.fresnel.image.x;
            npcContainer.fresnel.image.y = npcAnimation.fresnel.image.y;

            npcContainer.mouth = npcAnimation.mouth;

        }
            //end
            
        
        const serializedData = {
            containers: npcReferenceData.npcContainers,
            absolutes: npcPhysicsData.absolutes,
            positions: npcPhysicsData.positions
        }
        
        base.postMessage({type: "NPC Movement Data", payload: serializedData});
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Movement Clock: " + endTime);
        }
    }
    
    function lerp(start, end, t){
        return start * (1 - t) + end * t;
    }
}