export default () => {

    let base = self; // eslint-disable-line no-restricted-globals

    let startTime;
    let endTime;

    let gameFoliageData;
    let playerPhysicsData;
    let windDirectionX;
    let windDirectionY;
    let desiredSubTerrainOffsetX;
    let desiredSubTerrainOffsetY;
    let desiredTerrestialTerrainOffsetX;
    let desiredTerrestialTerrainOffsetY;
    let desiredAerialTerrainOffsetX;
    let desiredAerialTerrainOffsetY;

    base.onmessage = (event) => {
        processMessage(event.data);
    };

    const processMessage = (event) => {
        if(event.type == "Update"){
            update(event.payload);
        }
    }

    const update = (eventData) => {
        startTime = new Date().getTime();
        gameFoliageData = eventData.gameFoliageData;
        playerPhysicsData = eventData.playerPhysicsData;
        
        
        gameFoliageData.foliageMovementCounter = gameFoliageData.foliageMovementCounter + 1;
        if(gameFoliageData.foliageMovementCounter >= Math.random() * gameFoliageData.windDirectionChangeMax + gameFoliageData.windDirectionChangeMin){
            gameFoliageData.foliageMovementCounter = 0;
            
            gameFoliageData.windAerialTerrainOffsetX = gameFoliageData.windSubTerrainOffsetX;
            gameFoliageData.windAerialTerrainOffsetY = gameFoliageData.windSubTerrainOffsetY;
            gameFoliageData.windTerrestialTerrainOffsetX = gameFoliageData.windAerialTerrainOffsetX;
            gameFoliageData.windTerrestialTerrainOffsetY = gameFoliageData.windAerialTerrainOffsetY;
            
            windDirectionX = Math.cos(Math.PI * Math.round(Math.random()));
            windDirectionY = Math.cos(Math.PI * Math.round(Math.random()));
            gameFoliageData.windSubTerrainOffsetX = windDirectionX * gameFoliageData.windPower;
            gameFoliageData.windSubTerrainOffsetY = windDirectionY * gameFoliageData.windPower;
        }
        
        desiredSubTerrainOffsetX = gameFoliageData.windSubTerrainOffsetX;
        desiredSubTerrainOffsetY = gameFoliageData.windSubTerrainOffsetY;
        
        gameFoliageData.subTerrainOffsetX = lerp(gameFoliageData.subTerrainOffsetX, desiredSubTerrainOffsetX, gameFoliageData.windSubTerrainSpeed);
        gameFoliageData.subTerrainOffsetY = lerp(gameFoliageData.subTerrainOffsetY, desiredSubTerrainOffsetY, gameFoliageData.windSubTerrainSpeed);

        desiredTerrestialTerrainOffsetX = gameFoliageData.windTerrestialTerrainOffsetX;
        desiredTerrestialTerrainOffsetY = gameFoliageData.windTerrestialTerrainOffsetY;
        
        gameFoliageData.terrestialTerrainOffsetX = lerp(gameFoliageData.terrestialTerrainOffsetX, desiredTerrestialTerrainOffsetX, gameFoliageData.windTerrestialTerrainSpeed);
        gameFoliageData.terrestialTerrainOffsetY = lerp(gameFoliageData.terrestialTerrainOffsetY, desiredTerrestialTerrainOffsetY, gameFoliageData.windTerrestialTerrainSpeed);    
        
        desiredAerialTerrainOffsetX = (playerPhysicsData.playerOffsetX * 0.01) + gameFoliageData.windAerialTerrainOffsetX - gameFoliageData.aerialTerrainInitialOffset;
        desiredAerialTerrainOffsetY = (playerPhysicsData.playerOffsetY * 0.01) + gameFoliageData.windAerialTerrainOffsetY - gameFoliageData.aerialTerrainInitialOffset;
        
        gameFoliageData.aerialTerrainOffsetX = lerp(gameFoliageData.aerialTerrainOffsetX, desiredAerialTerrainOffsetX, gameFoliageData.windAerialTerrainSpeed);
        gameFoliageData.aerialTerrainOffsetY = lerp(gameFoliageData.aerialTerrainOffsetY, desiredAerialTerrainOffsetY, gameFoliageData.windAerialTerrainSpeed);
   
        base.postMessage({type: "Game Foliage Data", payload: gameFoliageData });
        
        if(eventData.debug){
            endTime = new Date().getTime() - startTime;
            console.log("Foliage Clock: " + endTime);
        }
    }
    
    const lerp = (start, end, t) => {
        return start * (1 - t) + end * t;
    }
}