import React, { useState, useEffect, useRef } from 'react';
import { useGlobalState  } from '../../globalcontext';

const CrystalClouds = () => {
    
    const [ , forceRerender] = useState();

    const clouds = useRef([]);
    
    const { engineActiveStatus, chatRoomActiveStatus, videoPlayerActiveStatus } = useGlobalState();
    
    const engineActiveStatusReference = useState(0);
    const chatRoomActiveStatusReference= useState(0);
    const videoPlayerActiveStatusReference = useState(0);
    
    const initialized = useRef(false);
    
    const cloudReferences = useRef([]);
    
    const minFadeInterval = useRef(3);
    const maxFadeInterval = useRef(5);
    const maxFadeOpacity = useRef(40);
    
    const cloudCount = useRef(14);

    useEffect(() => {
    
          // Set interval for randomizing clouds
        const intervalId = setInterval(() => {
            if(engineActiveStatusReference.current !== 3 && chatRoomActiveStatusReference.current !== 3 && videoPlayerActiveStatusReference.current !== 3){
                adjustClouds(); 
            }
        }, 201);
        
        if(initialized.current){
            generateClouds();
        }

        initialized.current = true;
        return () => {
            clearInterval(intervalId);
        };
    }, []);
    
    useEffect(() => {
    
        engineActiveStatusReference.current = engineActiveStatus;
        chatRoomActiveStatusReference.current = chatRoomActiveStatus;
        videoPlayerActiveStatusReference.current = videoPlayerActiveStatus;

        return () => {
            
        };
    }, [engineActiveStatus, chatRoomActiveStatus, videoPlayerActiveStatus]);

    const generateClouds = () => {
        const newClouds = [];

        for (let i = 0; i < cloudCount.current; i++) {
            const cloud = {
                top: `${Math.floor(Math.random() * 140) - 10}%`,
                left: `${Math.floor(Math.random() * 90) - 10}%`,
                opacity: 0,
                counter: Math.floor(Math.random() * 15),
                fade: 'Out',
                fadeInterval: Math.floor(Math.random() * maxFadeInterval.current),
            };
            newClouds.push(cloud);
        }
        clouds.current = newClouds;
        forceRerender({});
    };
    
    const adjustClouds = () => {
        for (let cloudIndex = 0; cloudIndex < clouds.current.length; cloudIndex++) {
            if(clouds.current[cloudIndex].fade === 'Out'){
                clouds.current[cloudIndex].counter += clouds.current[cloudIndex].fadeInterval;
                if(clouds.current[cloudIndex].counter >= maxFadeOpacity.current){
                    clouds.current[cloudIndex].fade = 'In';
                }
            }
            if(clouds.current[cloudIndex].fade === 'In'){
                clouds.current[cloudIndex].counter -= clouds.current[cloudIndex].fadeInterval;
                if(clouds.current[cloudIndex].counter <= 0){
                    clouds.current[cloudIndex].fade = 'Out';
                    clouds.current[cloudIndex].fadeInterval = Math.floor(Math.random() * maxFadeInterval.current) + minFadeInterval.current;
                    cloudReferences.current[cloudIndex].style.top = `${Math.floor(Math.random() * 140) - 10}%`;
                    cloudReferences.current[cloudIndex].style.left = `${Math.floor(Math.random() * 90) - 10}%`;
                }
            }
            
            cloudReferences.current[cloudIndex].style.opacity = clouds.current[cloudIndex].counter * .01;
        }
    };

    return (
        <div className="crystal-clouds-container">
            {clouds.current.map((cloud, index) => (
                <div
                    ref={(ref) => (cloudReferences.current[index] = ref)}
                    key={index}
                    className="crystal-cloud"
                    style={{
                        top: cloud.top,
                        left: cloud.left,
                        opacity: cloud.opacity,
                        animationDelay: `${cloud.delay}s`,
                    }}
                ></div>
            ))}
        </div>
    );
};

export default CrystalClouds;