let bitmaps;

const importBitmaps = async () => {
    
    return new Promise((resolve, reject) => {
        const importAll = (r) => {
            return r.keys().map(r);
        }
        
        const imageContext = require.context('../../../public/images/video/profile', true, /\.(png|jpe?g|svg)$/, 'lazy');
        const imageFileNames = imageContext.keys();

        const loadedImages = [];

        // Load each image asynchronously
        Promise.all(imageFileNames.map(async (imageFileName) => {
            // Load image data
            const image = await imageContext(imageFileName);

            // Extract image name from file path
            const imageName = imageFileName.match(/([^/]+)\.(png|jpe?g|svg)$/)[1];

            // Push image object to the loadedImages array
            loadedImages.push({ name: imageName, data: image.default });
        })).then(() => {
            
            
            const serializedBitmaps = [
                { name: "CageTop", data: findImageByName('CageTop', loadedImages) },
                { name: "CageBottom", data: findImageByName('CageBottom', loadedImages) },
                { name: "CageBottomDim", data: findImageByName('CageBottomDim', loadedImages) },
                { name: "PortalTop", data: findImageByName('PortalTop', loadedImages) },
                { name: "PortalBottom", data: findImageByName('PortalBottom', loadedImages) },
                { name: "PortalBottomDim", data: findImageByName('PortalBottomDim', loadedImages) },
                { name: "AltarTop", data: findImageByName('AltarTop', loadedImages) },
                { name: "AltarBottom", data: findImageByName('AltarBottom', loadedImages) },
                { name: "AltarBottomDim", data: findImageByName('AltarBottomDim', loadedImages) },
                { name: "WallTop", data: findImageByName('WallTop', loadedImages) },
                { name: "WallBottom", data: findImageByName('WallBottom', loadedImages) },
                { name: "WallBottomDim", data: findImageByName('WallBottomDim', loadedImages) },
                { name: "PillarTop", data: findImageByName('PillarTop', loadedImages) },
                { name: "PillarBottom", data: findImageByName('PillarBottom', loadedImages) },
                { name: "PillarBottomDim", data: findImageByName('PillarBottomDim', loadedImages) },

            ];

            bitmaps = serializedBitmaps;
            resolve(bitmaps);
            
        }).catch((error) => {
            reject(error);
        });
         
        const findImageByName = (imageName, loadedImages) => {
            const lowerCaseImageName = imageName.toLowerCase();
            return loadedImages.find(image => image.name.toLowerCase() === lowerCaseImageName);
        };
         
    });

}; 

const loadBitmaps = () => {
    return bitmaps;
}


export default {
    importBitmaps,
    loadBitmaps,
};