import React, { useState, useEffect } from 'react';

const CrystalLightning = () => {
    const areaRadius = 160; // Radius of the circular area
    const areaDiameter = areaRadius * 2; // Diameter of the circular area
    const [position, setPosition] = useState({ x: areaRadius, y: areaRadius });
    const [velocity, setVelocity] = useState({ vx: 5, vy: 2 });
    const [rotation, setRotation] = useState(0);

      // Updates the velocity of the ball if it hits the boundaries of the circular area
    const updateVelocity = () => {
        const newPosition = { ...position };
        const newVelocity = { ...velocity };
        
        newPosition.x += newVelocity.vx * 15;
        newPosition.y += newVelocity.vy * 15;
        
        setPosition(newPosition);
        
        const differenceX = areaRadius - newPosition.x;
        const differenceY = areaRadius - newPosition.y;
        
        const distance = Math.sqrt(differenceX * differenceX + differenceY * differenceY);

        if(distance > areaRadius - 100){
            if(Math.abs(differenceX) > Math.abs(differenceY)){
                newVelocity.vx *= -1;
            }
            if(Math.abs(differenceY) > Math.abs(differenceX)){
                newVelocity.vy *= -1;
            }
        }
        
        setRotation(differenceX);
        setVelocity(newVelocity); 
    };

    useEffect(() => {
        const animationFrame = requestAnimationFrame(() => {
              updateVelocity();
        });

        return () => cancelAnimationFrame(animationFrame);
    });
    
    const requestAnimationFrame = (callback) => {
        const currentTime = Date.now();
        const timeToCall = Math.max(0, 16 - (currentTime - lastTime));
        const id = setTimeout(() => callback(currentTime + timeToCall), timeToCall);
        lastTime = currentTime + timeToCall;
        return id;
    };
    
    let lastTime = Date.now();
    
    return (
        <div className="crystal-lightning"
            style={{
                transform: `rotate(${rotation}deg)`,
                top: position.y - areaRadius/2,
                left: position.x - areaRadius/2,
            }}
        ></div>  
    );
};

export default CrystalLightning;